import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { global } from './global';
import { Per } from '../models/personal';
import { Observable } from 'rxjs';

@Injectable()

export class PerService {
    public url: string;
    public identity;
    public token;
    public rol;

    constructor(public _http: HttpClient) {
        this.url = global.url;
    }

    register(per: Per): Observable<any> {
        let params = JSON.stringify(per);
        let headers = new HttpHeaders().set('Content-Type', 'application/json')
            .set('Authorization', this.getToken());

        return this._http.post(this.url + 'registrarper', params, { headers: headers })
    }

    getIdentity() {
        let identity = JSON.parse(localStorage.getItem('identity'));

        if (identity != 'undefined') {
            this.identity = identity;
        } else {
            this.identity = null;
        }

        return this.identity;
    }

    getToken() {
        let token = localStorage.getItem('token');

        if (token != 'undefined') {
            this.token = token;
        } else {
            this.token = null;
        }

        return this.token;
    }

    updateper(per: Per): Observable<any> {
        const params = JSON.stringify(per);
        const headers = new HttpHeaders().set('Content-Type', 'application/json')
            .set('Authorization', this.getToken());

        return this._http.put(this.url + 'actualizarper/' + per._id, params, { headers:headers});

    }
    getpers(): Observable<any> {
        const headers = new HttpHeaders().set('Content-Type', 'application/json')
            .set('Authorization', this.getToken());
        return this._http.get(this.url + 'listarpers', { headers });
    }
    getper(id): Observable<any> {
        const headers = new HttpHeaders().set('Content-Type', 'application/json')
            .set('Authorization', this.getToken());
        return this._http.get(this.url + 'verpers/' + id, { headers });
    }
    findper(id): Observable<any> {
        const headers = new HttpHeaders().set('Content-Type', 'application/json')
            .set('Authorization', this.getToken());
        return this._http.get(this.url + 'findpers/' + id, { headers });
    }
    cambiarEstado(personal: Per): Observable<any> {
        let params = JSON.stringify(personal);
        const headers = new HttpHeaders().set('Content-Type', 'application/json')
                                         .set('Authorization', this.getToken());
        console.log('cambiando estado....');
        
        return this._http.put(this.url + 'cambiar-estadoper', params , {headers});
    }
    getByCi(ci): Observable<any> {
        const headers = new HttpHeaders().set('Content-Type', 'application/json')
                                         .set('Authorization', this.getToken());
        return this._http.get(this.url + 'ver-por-ci/' + ci , {headers});
    }
    updatep(per: Per): Observable<any>{
        let params = JSON.stringify(per);
        let headers = new HttpHeaders().set('Content-Type', 'application/json')
                                       .set('Authorization', this.getToken());
        return this._http.put(this.url+ 'editar-p/' + per._id, params, {headers:headers})
    }
}
