// tslint:disable: variable-name
import { Component } from '@angular/core';
import { APService } from '../../../services/ap.service';
import { AP } from '../../../models/accion_personal';
import { Router } from '@angular/router';
import { FuncService } from '../../../services/fun.service';
import Swal from 'sweetalert2';
import {formatDate } from '@angular/common';

@Component({
    selector: 'app-listap',
    templateUrl: './accion_personal.component.html',
    providers: [APService, FuncService]
})

export class ListarAPComponent {
    public title: string;
    public APs: any[] = [];
    public noList: boolean;
    public Searchap: string;
    public bufferap: any[] = [];
    public identity;
    public today;
    constructor(
        private _apService: APService,
        private _funcService: FuncService,
        private _router: Router
    ) {
        this.title = 'Listado de Acciones de Personal';
        this.initAPList();
        this.identity = this._funcService.getIdentity();
        this.today = new Date();
        this.identity = JSON.parse(localStorage.getItem('identity'));
        if (!this.identity) {
            this._router.navigate(['/home']);
        }
    }
    editAP(id) {
        this._router.navigate(['/regap', id]);
    }
    initAPList() {
        this._apService.getAps().subscribe(
            response => {
                this.APs = response.aps;
                this.bufferap = response.aps;
                console.log(this.APs);
            }, error => {
                console.error(error as any);
            }
        )
    }
    titleEdicion(id): string {
        let message: string;
        if (id == this.identity._id) {
            message = 'Editar la Accion del Personal';
        } else {
            message = 'Solo puedes editar TUS Acciones de Personal';
        }
        return message;
    }
    cambioEstado(accion) {
        if (this.identity.role == 'admin') {
            Swal.fire({
                title: 'Desea aprobar esta accion de personal?',
                text: "Una vez aprobada no podra ser editada",
                icon: 'warning',
                showCancelButton: true,
                confirmButtonColor: '#3085d6',
                cancelButtonColor: '#d33',
                confirmButtonText: 'Si, Aprobar!'
            }).then((result) => {
                if (result.value) {
                    Swal.fire(
                        'Activada!',
                        'La accion de personal ha sido aprobada',
                        'success'
                    )
                    if (accion.activa === true) {
                        accion.activa = false;
                    } else {
                        accion.activa = true;
                        accion.fecha_activacion = this.today;
                    }
                    this._apService.cambiarEstado(accion).subscribe(
                        response => {
                            console.log(response);
    
                        }, error => {
                            console.log(error as any);
    
                        }
                    );
                }
            })


        }

    }
    find(termino) {
        termino = termino.toLowerCase().replace(/ /g, '');
        // termino = termino.split(';');
        // console.log(termino);
        this.APs = [];
        if (this.bufferap && this.bufferap.length >= 1) {
            for (const ap of this.bufferap) {
                let datos = ap.ci_per + ap.nombres + ap.apellidos + ap.codigo_ap + ap.quipux + ap.motivo + ap.fecha + ap.tipo + ap.autor.usuario;
                if (ap.nombres) {
                    datos = datos + ap.nombres;
                }
                datos = datos.toLowerCase().replace(/ /g, '');
                datos = datos.toLowerCase().replace(/-/g, '');
                if ((datos.indexOf(termino)) >= 0) {
                    this.APs.push(ap);
                }
            }
        }
    }
}
