import {Injectable} from '@angular/core';
import { HttpClient, HttpHeaders} from '@angular/common/http';
import { Observable } from 'rxjs';
import {global} from './global';
import {RL} from '../models/regimen_laboral';

@Injectable()

export class RLService{
    public url: string;
    public identity;
    public token;
    public rol;

    constructor(public _http: HttpClient){
        this.url = global.url;
    }

    register(rl: RL): Observable<any>{
        let params = JSON.stringify(rl);
        let headers = new HttpHeaders().set('Content-Type', 'application/json')
                                    .set('Authorization', this.getToken());

        return this._http.post(this.url+ 'registrarrl', params, {headers:headers})
    }

    getIdentity(){
        let identity = JSON.parse(localStorage.getItem('identity'));

        if(identity != 'undefined'){
            this.identity = identity;
        }else{
            this.identity = null;
        }

        return this.identity;
    }
    
    getToken(){
        let token = localStorage.getItem('token');

        if(token != 'undefined'){
            this.token = token;
        }else{
            this.token = null;
        }

        return this.token;
    }

    // updateFun(fun : Func): Observable<any>{
    //     let params = JSON.stringify(fun);
    //     let headers = new HttpHeaders().set('Content-Type', 'application/json')
    //                                    .set('Authorization', this.getToken());

    //     return this._http.put(this.url+'actualizarfun/'+fun._id, params, {headers: headers});

    // }
    getrl(): Observable<any> {
        const headers = new HttpHeaders().set('Content-Type', 'application/json')
          .set('Authorization', this.getToken());
        return this._http.get(this.url + 'listarrl', { headers });
      }

      getrls(id): Observable<any> {
        const headers = new HttpHeaders().set('Content-Type', 'application/json')
          .set('Authorization', this.getToken());
        return this._http.get(this.url + 'listarrls/' + id, { headers });
      }

      updaterl(rl: RL): Observable<any>{
        const params = JSON.stringify(rl);
        const headers = new HttpHeaders().set('Content-Type', 'application/json')
        .set('Authorization', this.getToken());
        return this._http.put(this.url + 'editarrl/'+rl._id, params , {headers});
      }

}