import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { PAD } from '../../../models/puesto_adicional';
import { PAService } from '../../../services/pad.service';
import { FuncService } from '../../../services/fun.service';

@Component({
    selector: 'listapa',
    templateUrl: './puesto_adicional.component.html',
    providers: [PAService, FuncService]
})

export class ListarpaComponent {
    public title: String;
    public pa: PAD;
    public Searchpa: string;
    public pas: PAD[] = [];
    public bufferpa: PAD[] = [];
    public identity;

    constructor(
        private _paService: PAService,
        private _funcService: FuncService,
        private _router: Router
    ) {
        this.title = 'Listar Puestos de Trabajo';
        this.initpa();
        this.identity = this._funcService.getIdentity();
        this.identity = JSON.parse(localStorage.getItem('identity'));
        if (!this.identity) {
            this._router.navigate(['/home']);
          }
    }

    initpa() {
        this._paService.getpa().subscribe(
            response => {
                this.pas = response.PADI;
                this.bufferpa = response.PADI;
                console.log(this.pas);   
            }, error => {
                console.error(error as any);
            }
        )
    }
    
    getData(event) {
        this.pas = JSON.parse(event);

    }
    find(termino) {
        termino = termino.toLowerCase().replace(/ /g, '');
        this.pas = [];
        if (this.bufferpa && this.bufferpa.length >=1 ) {
            for (const anc of this.bufferpa) {
                let datos = anc.puesto_adicional + anc.codigo;
                if (anc.puesto_adicional) {
                    datos = datos + anc.puesto_adicional;
                }
                datos = datos.toLowerCase().replace(/ /g, '');
                datos = datos.toLowerCase().replace(/-/g, '');
                if ((datos.indexOf(termino)) >= 0) {
                    this.pas.push(anc);
                    
                }
            }
        }
    }


}