import {Injectable} from '@angular/core';
import { HttpClient, HttpHeaders} from '@angular/common/http';
import { Observable } from 'rxjs';
import {global} from './global';
import {AN} from '../models/autoridad_nomidora';

@Injectable()

export class ANService{
    public url: string;
    public identity;
    public token;
    public rol;

    constructor(public _http: HttpClient){
        this.url = global.url;
    }

    register(an: AN): Observable<any>{
        let params = JSON.stringify(an);
        let headers = new HttpHeaders().set('Content-Type', 'application/json')
                                    .set('Authorization', this.getToken());

        return this._http.post(this.url+ 'registraran', params, {headers:headers})
    }

    getIdentity(){
        let identity = JSON.parse(localStorage.getItem('identity'));

        if(identity != 'undefined'){
            this.identity = identity;
        }else{
            this.identity = null;
        }

        return this.identity;
    }
    
    getToken(){
        let token = localStorage.getItem('token');

        if(token != 'undefined'){
            this.token = token;
        }else{
            this.token = null;
        }

        return this.token;
    }

    // updateFun(fun : Func): Observable<any>{
    //     let params = JSON.stringify(fun);
    //     let headers = new HttpHeaders().set('Content-Type', 'application/json')
    //                                    .set('Authorization', this.getToken());

    //     return this._http.put(this.url+'actualizarfun/'+fun._id, params, {headers: headers});

    // }
    getans(): Observable<any> {
        const headers = new HttpHeaders().set('Content-Type', 'application/json')
          .set('Authorization', this.getToken());
        return this._http.get(this.url + 'listaran', { headers });
      }
    getan(id): Observable<any> {
        const headers = new HttpHeaders().set('Content-Type', 'application/json')
          .set('Authorization', this.getToken());
        return this._http.get(this.url + 'listarans/' + id, { headers });
      }

      updateAn(an: AN): Observable<any>{
        const params = JSON.stringify(an);
        const headers = new HttpHeaders().set('Content-Type', 'application/json')
        .set('Authorization', this.getToken());
        return this._http.put(this.url + 'editaran/'+an._id, params , {headers});
      }
    cambiarEstado(nominadora: AN): Observable<any> {
        let params = JSON.stringify(nominadora);
        const headers = new HttpHeaders().set('Content-Type', 'application/json')
                                         .set('Authorization', this.getToken());
        console.log('cambiando estado....');
        
        return this._http.put(this.url + 'estadoan', params , {headers});
    } 

}