import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { AN } from '../../../models/autoridad_nomidora';
import { ANService } from '../../../services/an.service';
import { FuncService } from '../../../services/fun.service';

@Component({
    selector: 'listan',
    templateUrl: './autoridad_nominadora.component.html',
    providers: [ANService, FuncService]
})

export class ListaranComponent {
    public title: String;
    public an: AN;
    public Searchans: string;
    public ans: AN[] = [];
    public bufferans: AN[] = [];
    public identity;

    constructor(
        private _anService: ANService,
        private _funcService: FuncService,
        private _router: Router
    ) {
        this.title = 'Listar Autoridades nominadoras';
        this.initAns();
        this.identity = this._funcService.getIdentity();

        this.identity = JSON.parse(localStorage.getItem('identity'));
        if (!this.identity) {
            this._router.navigate(['/home']);
        }
    }

    initAns() {
        this._anService.getans().subscribe(
            response => {
                this.ans = response.ANR;
                this.bufferans = response.ANR;
                console.log(this.ans);
            }, error => {
                console.error(error as any);
            }
        )
    }
    cambioEstado(nominadora) {
        let confirmacion = confirm('Seguro de que desea Cambiar el estado de ese nominadora?');
        if (this.identity.role === 'admin') {
            if (confirmacion === true) {
                if (nominadora.activa === true) {
                    nominadora.activa = false;
                } else {
                    nominadora.activa = true;
                }
                this._anService.cambiarEstado(nominadora).subscribe(
                    response => {
                        console.log(response);

                    }, error => {
                        console.log(error as any);

                    }
                );
            }
        }

    }
    getData(event) {
        this.ans = JSON.parse(event);

    }
    find(termino) {
        termino = termino.toLowerCase().replace(/ /g, '');
        this.ans = [];
        if (this.bufferans && this.bufferans.length >= 1) {
            for (const anc of this.bufferans) {
                let datos = anc.nombre;
                if (anc.nombre) {
                    datos = datos + anc.nombre;
                }
                datos = datos.toLowerCase().replace(/ /g, '');
                datos = datos.toLowerCase().replace(/-/g, '');
                if ((datos.indexOf(termino)) >= 0) {
                    this.ans.push(anc);

                }
            }
        }
    }


}
