import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { RL } from '../../../models/regimen_laboral';
import { RLService } from '../../../services/rl.service';
import { FuncService } from '../../../services/fun.service';

@Component({
    selector: 'listrl',
    templateUrl: './regimen_laboral.component.html',
    providers: [RLService, FuncService]
})

export class ListarrlComponent {
    public title: String;
    public rl: RL;
    public Searchrl: string;
    public rls: RL[] = [];
    public bufferrl: RL[] = [];
    public identity;

    constructor(
        private _rlService: RLService,
        private _funcService: FuncService,
        private _router: Router,

    ) {
        this.title = 'Listar Regimenes Laborales';
        this.initrl();
        this.identity = this._funcService.getIdentity();
        this.identity = JSON.parse(localStorage.getItem('identity'));
        if (!this.identity) {
            this._router.navigate(['/home']);
        }
    }

    initrl() {
        this._rlService.getrl().subscribe(
            response => {
                this.rls = response.RLA;
                this.bufferrl = response.RLA;
                console.log(this.rls);
            }, error => {
                console.error(error as any);
            }
        )
    }

    getData(event) {
        this.rls = JSON.parse(event);

    }
    find(termino) {
        termino = termino.toLowerCase().replace(/ /g, '');
        this.rls = [];
        if (this.bufferrl && this.bufferrl.length >= 1) {
            for (const anc of this.bufferrl) {
                let datos = anc.regimen_laboral + anc.codigo;
                if (anc.regimen_laboral) {
                    datos = datos + anc.regimen_laboral;
                }
                datos = datos.toLowerCase().replace(/ /g, '');
                datos = datos.toLowerCase().replace(/-/g, '');
                if ((datos.indexOf(termino)) >= 0) {
                    this.rls.push(anc);

                }
            }
        }
    }


}