// tslint:disable: variable-name
// tslint:disable: component-selector
// tslint:disable: whitespace
// tslint:disable: curly
import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { AP } from '../../../models/accion_personal';
import { APService } from '../../../services/ap.service';
import { PerService } from '../../../services/pers.service';
import { Per } from '../../../models/personal';
import { DEService } from '../../../services/de.service';
import { RRService } from '../../../services/rr.service';
import { RHService } from '../../../services/rh.service';
import { ANService } from '../../../services/an.service';
import { RH } from '../../../models/recursos_humanos';
import { RR } from '../../../models/responsable_registro';
import { AN } from '../../../models/autoridad_nomidora';
import { FuncService } from './../../../services/fun.service';
import {formatDate } from '@angular/common';

@Component({
  selector: 'regap',
  templateUrl: './accion_personal.component.html',
  providers: [APService, PerService, DEService, RRService, RHService, ANService, FuncService],
})

export class RegisterapComponent {
  public title: string;
  public ap: AP;
  public status: string;
  public motivoExtra: string;
  public today: Date;
  public pers: any;
  public per: Per;
  public lista_quipux: any[];
  public editing: boolean;
  public bottonLabel: string;
  public listacodigo_ap: any[];
  public codigo_unica: boolean;
  public quipux_unica: boolean;
  public identity;
  public jstoday = '';
  

  public an: AN;
  public ans: AN[] = [];
  public bufferan: AN[] = [];

  public rr: RR;
  public rrs: RR[] = [];
  public bufferrr: RR[] = [];

  public rh: RH;
  public rhs: RH[] = [];
  public bufferrh: RH[] = [];
  constructor(
    private _route: ActivatedRoute,
    private _router: Router,
    private _apService: APService,
    private _perService: PerService,
    private _deService: DEService,
    private _rrService: RRService,
    private _anService: ANService,
    private _rhService: RHService,
    private _funService: FuncService
  ) {
    this.title = 'Registro de Acciones de Personal';
    this.initAP();
    this.today = new Date();
    this.jstoday = formatDate(this.today, 'dd/MM/yyyy', 'en-US', '+0500');
    this.editing = false;
    this.bottonLabel = 'REGISTRAR';
    this.recibirPersonal();
    this.initrr();
    this.initrh();
    this.initan();
    this.identity = this._funService.getIdentity();
    this.identity = JSON.parse(localStorage.getItem('identity'));
    if (!this.identity) {
      this._router.navigate(['/home']);
    }
  }
  initrr() {
    this._rrService.getrr().subscribe(
      response => {
        this.rrs = [];
        for (const rr of response.RRE) {
          if (rr.activa === true) {
            this.rrs.push(rr);
          }
        }
        console.log(this.rrs);
      }, error => {
        console.error(error as any);
      }
    )
  }
  initan() {
    this._anService.getans().subscribe(
      response => {
        // this.ans = response.ANR;
        // this.bufferan = response.ANR;
        this.ans = [];
        for (const an of response.ANR) {
          if (an.activa === true) {
            this.ans.push(an);
          }
        }
        console.log(this.ans);
      }, error => {
        console.error(error as any);
      }
    )
  }
  initrh() {
    this._rhService.getrh().subscribe(
      response => {
        this.rhs = [];
        for (const rh of response.RHU) {
          if (rh.activa === true) {
            this.rhs.push(rh);
          }
        }
        console.log(this.rhs);
      }, error => {
        console.error(error as any);
      }
    )
  }
  recibirPersonal() {
    this._route.params.subscribe(
      params => {
        if (params.id) {
          this._perService.getper(params.id).subscribe(
            response => {
              if (response.PERS) {
                this.pers = response.PERS;
                this.initQuipux(this.pers.cedula);
                this.rellenarPersonal();
                this.initcodigo_ap();
              }
            }, error => {
              console.log(error.status);
              if (error.status == 404) {
                console.log('buscando en acciones de personal');
                this._apService.getAp(params.id).subscribe(
                  responseAP => {
                    console.log(responseAP);
                    if (responseAP.AP) {
                      this.ap = responseAP.AP;
                      this.bottonLabel = 'EDITAR';
                      this.editing = true;
                    } else {
                      this.editing = false;
                    }
                  }
                );
              }
            }
          );
        }
      }
    );
  }

  rellenarPersonal() {
    if (this.pers && this.ap) {
      console.log(this.pers);
      this.ap.nombres = this.pers.nombres;
      this.ap.apellidos = this.pers.apellidos;
      this.ap.ci_per = this.pers.cedula;
      this.ap.lugar_de_trabajo_sa = this.pers.puesto_adicional.puesto_adicional;
      this.ap.puesto_sa = this.pers.escala_ocupacional.escala_ocupacional + '(' + this.pers.denominacion_puesto.denominacion_puesto + ')';
      this.ap.salario_sa = this.pers.rmu_puesto;
      this.ap.partida_presupuestaria_sa = this.today.getFullYear() + this.pers.estructura_programatica + '-' + this.pers.partida_individual;
      this.ap.res_reg = this.identity.titulo + this.identity.nombres + ' ' + this.identity.apellidos;
      this.ap.quipux = this.pers.memorandum;
      this.ap.codigo_ap = this.pers.codigoAP;
      this.ap.fecha = this.jstoday;

    }
  }
  rellenoCodigoRH(codigo: string) {
    const codigoArray = codigo.split('');
    if (codigoArray.length >= 3) {
      this.ap.codigo_rh = codigoArray[0] + codigoArray[1] + codigoArray[2] + '-REG-' + this.today.getFullYear();
    }
  }
  initAP() {
    this.ap = {
      _id: '',
      codigo_ap: '',
      codigo_rh: '',
      fecha: '',
      tipo: '',
      tipo_numero: '',
      quipux: '',
      tipo_fecha: '',
      apellidos: '',
      nombres: '',
      ci_per: '',
      no_af_iess: '',
      vigencia: '',
      explicacion: '',
      motivo: '',
      motivoExtra: '',
      proceso_sa: '',
      sbp_sa: '',
      puesto_sa: '',
      lugar_de_trabajo_sa: '',
      salario_sa: '',
      partida_presupuestaria_sa: '',
      proceso_sp: '',
      sbp_sp: '',
      puesto_sp: '',
      luga_de_trabajo_sp: '',
      salario_sp: '',
      partida_presupuestaria_sp: '',
      af_numero: '',
      af_fecha: '',
      activa: false,
      rec_hum: '',
      aut_nom: '',
      res_reg: '',
      caucion: '',
      caucion_fecha: '',
      caucion_exp: '',
      remplaza: '',
      rem_puesto: '',
      ceso_fun: '',
      num_ac_rem: '',
      fec_ac_rem: '',
      afi_col: '',
      cod_rem: '',
      fecha_rem: '',
      lugar: 'PUYO',
      fecha_fi: '',
      fecha_activacion: '',
      nombre_pc: '',
      cedula_pc: '',
      autor: ''
    };
  }

  initQuipux(cedula) {
    this._deService.getQuipux(cedula).subscribe(
      response => {
        if (response.listaQuipux && response.listaQuipux.length >= 1) {
          this.lista_quipux = response.listaQuipux;
        }
        console.log(this.lista_quipux);
      }
    );
  }
  comprobacionQuipux(quipux) {
    quipux = quipux.toUpperCase();
    console.log(quipux);
    let conteo = 0;
    if (this.lista_quipux && this.lista_quipux.length >= 1) {
      for (const acciones of this.lista_quipux) {
        const quipuxIngresado = acciones.quipux.toUpperCase();
        if (quipux == quipuxIngresado) {
          conteo++;
        }
      }
    } else conteo = 0;
    if (conteo >= 1) {
      alert('Quipux ya Ingresado con Anterioridad');
      this.quipux_unica = false;
    } else {
      this.quipux_unica = true;
      console.log('quipux unico');
    }
  }
  initcodigo_ap() {
    this._deService.getcodigoap().subscribe(
      response => {
        this.listacodigo_ap = response.listacodigo_ap;
        console.log(this.listacodigo_ap);
      }
    );
  }
  codigo_ap_Unica(codigo_ap) {
    codigo_ap = codigo_ap.toUpperCase();
    console.log(codigo_ap);
    let conteo = 0;
    if (this.listacodigo_ap && this.listacodigo_ap.length >= 1) {
      for (const acciones of this.listacodigo_ap) {
        const codigoIngresado = acciones.codigo_ap.toUpperCase();
        if (codigo_ap == codigoIngresado) {
          conteo++;
        }
      }
    } else conteo = 0;
    if (conteo >= 1) {
      alert('Codigo ya Ingresado con Anterioridad');
      this.codigo_unica = false;
    } else {
      this.codigo_unica = true;
      console.log('Codigo unico');
    }
  }
  onSubmit(form) {
    if (this.editing == false) {
      this.onNew(form);
    } else if (this.editing == true) {
      this.onEdit(form);
    }
  }

  onNew(form) {
    if (this.ap.motivo == 'OTRO') this.ap.motivoExtra = this.motivoExtra;
    this._apService.register(this.ap).subscribe(
      response => {
        if (response.AP && response.AP._id) {
          console.log(response.AP);
          this.status = 'success';
          this.removeFunc(response.AP.ci_per);
          this._router.navigate(['/impap', response.AP._id]);
        } else {
          this.status = 'error';
        }
      },
      error => {
        console.log(error as any);
      }
    );
  }
  onEdit(form) {
    if (this.ap.motivo == 'OTRO') this.ap.motivoExtra = this.motivoExtra;
    this._apService.update(this.ap).subscribe(
      response => {
        if (response.AP && response.AP._id) {
          console.log(response.AP);
          this.status = 'success';
          this._router.navigate(['/impap', response.AP._id]);
        } else {
          this.status = 'error';
        }
      },
      error => {
        console.log(error as any);
      }
    );
  }

  spfull() {
    if (this.pers && this.ap) {
      console.log(this.pers);
      this.ap.salario_sp = this.pers.rmu_puesto;
      this.ap.partida_presupuestaria_sp = this.today.getFullYear() + this.pers.estructura_programatica + '-' + this.pers.partida_individual;
    }
  }
  spclear() {
    if (this.pers && this.ap) {
      console.log(this.pers);
      this.ap.salario_sp = '';
      this.ap.partida_presupuestaria_sp = '';
    }
  }
  pcfull() {
    if (this.pers && this.ap) {
      console.log(this.pers);
      this.ap.nombre_pc = this.ap.apellidos + ' ' + this.ap.nombres;
      this.ap.cedula_pc = this.ap.ci_per;
    }
  }
  pcclear() {
    if (this.pers && this.ap) {
      console.log(this.pers);
      this.ap.nombre_pc = '';
      this.ap.cedula_pc = '';
    }
  }
  clear() {
    this.ap.tipo_numero = '';
    this.ap.tipo_fecha = '';
  }
  removeFunc(ci) {
    this._perService.getByCi(ci).subscribe(
      responsePer => {
        console.log(responsePer.PER);
        this.per = responsePer.PER[0];
        this._perService.updatep(this.per).subscribe(
          response => {
            console.log(response);
          }
        );
      }
    );

  }
}

