import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { ML } from '../../../models/modalidad_laboral';
import { MLService } from '../../../services/ml.service'
import { DEService } from '../../../services/de.service';
@Component({
    selector: 'regml',
    templateUrl: './modalidad_laboral.component.html',
    providers: [MLService, DEService]
})

export class RegistermlComponent {
    public title: String;
    public ml: ML;
    public status: string;
    public status1: string;
    public editar: boolean;
    public alln_unica: boolean;
    public identity;
    public alls: any[];
    constructor(
        private _route: ActivatedRoute,
        private _router: Router,
        private _mlService: MLService,
        private _deService: DEService
    ) {
        this.title = 'Registro de Modalidad Laboral';
        this.editar = false;
        this.initML();
        this.initMLEdit();
        this.initall();
        this.identity = JSON.parse(localStorage.getItem('identity'));
        if (!this.identity) {
            this._router.navigate(['/home']);
          }
    }
    initall() {
        this._deService.getmla().subscribe(
            response => {
                this.alls = response.ml;
                console.log(this.alls);
            }
        );
    }
    allnUnica(modalidad_laboral) {
        this.alln_unica = true;
        for (const alls of this.alls) {
            const allBD = alls.modalidad_laboral;
             //console.log(allBD);
            if (allBD === modalidad_laboral) {

                this.alln_unica = false;
            }
        }
    }
    initML() {
        this.ml = new ML('',
            '',
            '',
            '',
            '',
            '');
    }
    initMLEdit() {
        this._route.params.subscribe(
            params => {
                console.log(params.id);
                if (params.id) {
                    this._mlService.getmls(params.id).subscribe(
                        response => {
                            if (response.ML) {
                                this.ml = response.ML;
                                this.editar = true;
                            }
                        }
                    )
                }
            }
        )
    }
    onSubmit(form) {
        if (this.editar == true) {
            this.onEdit(form);
        } else if (this.editar == false) {
            this.onNew(form);
        }
    }
    onEdit(form) {
        this._mlService.updateml(this.ml).subscribe(
            response => {
                if (response.ML && response.ML._id) {
                    this.status1 = 'success';

                } else {
                    this.status1 = 'error';
                }
            },
            error => {
                console.log(<any>error);
            }

        );
    }
    onNew(form) {
        this._mlService.register(this.ml).subscribe(
            response => {
                if (response.ML && response.ML._id) {
                    // console.log(response.fun);
                    this.status = 'success';
                    form.reset();
                    this.initall();
                } else {
                    this.status = 'error';
                }
            },
            error => {
                console.log(<any>error);
            }
        );
    }
}