  initFunc() {
    this._funcService.getfuns().subscribe(
      response => {
        this.funcs = response.FUNC;
        console.log(this.funcs);

      }
    );
  }
  initAPList() {
    this._apService.getAps().subscribe(
      response => {
        this.APs = response.aps;
        this.contador = this.APs.length;
        this.TempAPs = this.APs;
        this.bufferap = response.aps;
        this.getContadorestipo();
        this.getContadoresmotivo();
        this.initAutorContador();
        console.log(this.APs);
      }, error => {
        console.error(error as any);
      }
    );
  }

  getContadorestipo() {
    this.acuerdo = 0;
    this.decreto = 0;
    this.resolucion = 0;
    for (const ap of this.APs) {
      switch (ap.tipo.toLowerCase()) {
        case 'acuerdo':
          this.acuerdo++;
          break;
        case 'decreto':
          this.decreto++;
          break;
        case 'resolucion':
          this.resolucion++;
          break;
      }
    }
  }
  public autorContador: any[];
  initAutorContador() {
    this.autorContador = [];
    for (const funcionario of this.funcs) {
      const autor = {
        nombres: funcionario.ci_fun + ' - ' + '(' + funcionario.usuario + ')' + funcionario.nombres.toUpperCase() + funcionario.apellidos.toUpperCase(),
        acciones: 0
      };
      for (const acciones of this.APs) {
        console.log('acciones: ', acciones.autor);
        console.log('funcionario: ', funcionario._id);
        if (acciones.autor._id == funcionario._id) {
          autor.acciones++;
        }
      }
      this.autorContador.push(autor);
    }
  }
