  filtrarFecha() {
    if (this.fechaFin && this.fechaInicio) {
      let inicio = moment(this.fechaInicio);
      let fin = moment(this.fechaFin);
      if (inicio > fin) {
        alert('la fecha de inicio no puede ser anterior a la de fin');
      } else {
        console.log(fin.diff(inicio, 'days'), ' dias de diferencia');
        this.APs = [];
        for (let i = 0; i < this.bufferap.length; i++) {
          const ap = this.bufferap[i];
          const fecha = moment(ap.fecha);
          const diffInicio = fecha.diff(inicio, 'days');
          const diffFin = fin.diff(fecha, 'days');
          if (diffInicio >= 0 && diffFin >= 0) {
            console.log('fecha: ', ap.fecha, '. dif in: ', diffInicio, '. dif fin: ', diffFin);
            this.APs.push(ap);
          }
        }
        this.contador = this.APs.length;
        this.TempAPs = this.APs;
        this.getContadorestipo();
        this.initAutorContador();
        this.getContadoresmotivo();
      }
    }
  }
  public TempAPs = [];
  filtrarautor(autor: string) {
    autor = autor.toLocaleLowerCase();
    this.APs = [];
    for (let i = 0; i < this.TempAPs.length; i++) {
      const ap = this.TempAPs[i];
      const autorap = ap.autor._id.toLowerCase();
      if (autorap.indexOf(autor, 0) >= 0) {
        this.APs.push(ap);
        console.log(this.TempAPs.length);
      }

    }

    this.contador = this.APs.length;
    this.getContadorestipo();
    this.initAutorContador();
    this.getContadoresmotivo();
  }
  filtrarcedula(cedula: string) {
    cedula = cedula.toLocaleLowerCase();
    this.APs = [];
    for (let i = 0; i < this.TempAPs.length; i++) {
      const ap = this.TempAPs[i];
      const cedulaap = ap.ci_per.toLowerCase();
      if (cedulaap.indexOf(cedula, 0) >= 0) {
        this.APs.push(ap);
        console.log(this.TempAPs.length);

      }
    }
    this.contador = this.APs.length;
    this.getContadorestipo();
    this.initAutorContador();
    this.getContadoresmotivo();
  }
