import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { Func } from '../../../models/funcionario';
import { FuncService } from '../../../services/fun.service';

@Component({
  selector: 'app-password_admin',
  templateUrl: './password_admin.component.html',
  styleUrls: ['./password_admin.component.css'],
  providers: [FuncService]
})
export class Password_adminComponent {

  public title: string;
  public fun: Func;
  public status: string;
  public yut: boolean;
  public identity;

  constructor(
    private _route: ActivatedRoute,
    private _router: Router,
    private _funService: FuncService
  ) {
    this.title = 'Actualizar Contraseña';
    // this.fun = this._funService.getIdentity();
    this.initFunc();
    this.check = false;
    this.identity = JSON.parse(localStorage.getItem('identity'));
        if (!this.identity || this.identity.role === 'ROLE_USER') {
            this._router.navigate(['/home']);
        }
  }
  initFunc() {
    this._route.params.subscribe(
      params => {
        const id = params.id;
        this._funService.getfun(id).subscribe(
          response => {
            this.fun = response.FUN;
          }
        )
      }
    )
  }
  ngOnInit() {
    console.log('fun-edit-pass cargado');
    
  }
  cambiarcontra(passwordn){
    const id = this.fun._id;
    const actualizar = {
      password : passwordn
    }
    this._funService.updatepass(id, actualizar).subscribe(
      response => {
        console.log(response);    
      }
    )
  }
  public check :boolean;

  comparecod(pass1, pass2){
    if(pass1 === pass2){
      this.yut  = true;
    }else{
      this.yut = false;
    }

  }
}