import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { UO } from '../../../models/unidad_organica';
import { UOService } from '../../../services/uo.service';
import { FuncService } from '../../../services/fun.service';

@Component({
    selector: 'listuo',
    templateUrl: './unidad_organica.component.html',
    providers: [UOService, FuncService]
})

export class ListaruoComponent {
    public title: String;
    public uo: UO;
    public Searchuo: string;
    public uos: UO[] = [];
    public bufferuo: UO[] = [];
    public identity;

    constructor(
        private _uoService: UOService,
        private _funcService: FuncService,
        private _router:Router
    ) {
        this.title = 'Listar Unidades Organicas';
        this.inituo();
        this.identity = this._funcService.getIdentity();
        this.identity = JSON.parse(localStorage.getItem('identity'));
        if (!this.identity) {
            this._router.navigate(['/home']);
          }
    }

    inituo() {
        this._uoService.getuo().subscribe(
            response => {
                this.uos = response.UOR;
                this.bufferuo = response.UOR;
                console.log(this.uos);   
            }, error => {
                console.error(error as any);
            }
        )
    }
    
    getData(event) {
        this.uos = JSON.parse(event);

    }
    find(termino) {
        termino = termino.toLowerCase().replace(/ /g, '');
        this.uos = [];
        if (this.bufferuo && this.bufferuo.length >=1 ) {
            for (const anc of this.bufferuo) {
                let datos = anc.unidad_organica + anc.codigo;
                if (anc.unidad_organica) {
                    datos = datos + anc.unidad_organica;
                }
                datos = datos.toLowerCase().replace(/ /g, '');
                datos = datos.toLowerCase().replace(/-/g, '');
                if ((datos.indexOf(termino)) >= 0) {
                    this.uos.push(anc);
                    
                }
            }
        }
    }


}