import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { RR } from '../../../models/responsable_registro';
import { FuncService } from '../../../services/fun.service';
import { RRService } from '../../../services/rr.service';

@Component({
    selector: 'listrr',
    templateUrl: './responsable_registro.component.html',
    providers: [RRService, FuncService]
})

export class ListarrrComponent {
    public title: String;
    public rr: RR;
    public Searchrr: string;
    public rrs: RR[] = [];
    public bufferrr: RR[] = [];
    public identity;

    constructor(
        private _rrService: RRService,
        private _funcService: FuncService,
        private _router: Router
    ) {
        this.title = 'Listar Responsables del Registro';
        this.initrh();
        this.identity = this._funcService.getIdentity();
        this.identity = JSON.parse(localStorage.getItem('identity'));
        if (!this.identity) {
            this._router.navigate(['/home']);
          }
    }

    initrh() {
        this._rrService.getrr().subscribe(
            response => {
                this.rrs = response.RRE;
                this.bufferrr = response.RRE;
                console.log(this.rrs);   
            }, error => {
                console.error(error as any);
            }
        )
    }
    cambioEstado(rr){
        let confirmacion = confirm('Seguro de que desea Cambiar el estado del responsable del registro?');
        if (this.identity.role === 'admin')
        if (confirmacion === true) {
            if (rr.activa === true){
                rr.activa = false;
            } else {
                rr.activa = true;
            }
            this._rrService.cambiarEstado(rr).subscribe(
                response=> {
                    console.log(response);
                    
                }, error => {
                    console.log(error as any);
                    
                }
            );
        }
        
    }
    getData(event) {
        this.rrs = JSON.parse(event);

    }
    find(termino) {
        termino = termino.toLowerCase().replace(/ /g, '');
        this.rrs = [];
        if (this.bufferrr && this.bufferrr.length >=1 ) {
            for (const anc of this.bufferrr) {
                let datos = anc.nombre;
                if (anc.nombre) {
                    datos = datos + anc.nombre;
                }
                datos = datos.toLowerCase().replace(/ /g, '');
                datos = datos.toLowerCase().replace(/-/g, '');
                if ((datos.indexOf(termino)) >= 0) {
                    this.rrs.push(anc);
                    
                }
            }
        }
    }


}
