import {Injectable} from '@angular/core';
import { HttpClient, HttpHeaders} from '@angular/common/http';
import { Observable } from 'rxjs';
import {global} from './global';
import {RH} from '../models/recursos_humanos';

@Injectable()

export class RHService{
    public url: string;
    public identity;
    public token;
    public rol;

    constructor(public _http: HttpClient){
        this.url = global.url;
    }

    register(rh: RH): Observable<any>{
        let params = JSON.stringify(rh);
        let headers = new HttpHeaders().set('Content-Type', 'application/json')
                                    .set('Authorization', this.getToken());

        return this._http.post(this.url+ 'registrarrh', params, {headers:headers})
    }

    getIdentity(){
        let identity = JSON.parse(localStorage.getItem('identity'));

        if(identity != 'undefined'){
            this.identity = identity;
        }else{
            this.identity = null;
        }

        return this.identity;
    }
    
    getToken(){
        let token = localStorage.getItem('token');

        if(token != 'undefined'){
            this.token = token;
        }else{
            this.token = null;
        }

        return this.token;
    }

    // updateFun(fun : Func): Observable<any>{
    //     let params = JSON.stringify(fun);
    //     let headers = new HttpHeaders().set('Content-Type', 'application/json')
    //                                    .set('Authorization', this.getToken());

    //     return this._http.put(this.url+'actualizarfun/'+fun._id, params, {headers: headers});

    // }
    getrh(): Observable<any> {
        const headers = new HttpHeaders().set('Content-Type', 'application/json')
          .set('Authorization', this.getToken());
        return this._http.get(this.url + 'listarrh', { headers });
      }
      
      cambiarEstado(rh: RH): Observable<any> {
        let params = JSON.stringify(rh);
        const headers = new HttpHeaders().set('Content-Type', 'application/json')
                                         .set('Authorization', this.getToken());
        console.log('cambiando estado....');
        
        return this._http.put(this.url + 'estadorh', params , {headers});
    } 

    getrhs(id): Observable<any> {
        const headers = new HttpHeaders().set('Content-Type', 'application/json')
          .set('Authorization', this.getToken());
        return this._http.get(this.url + 'listarrhs/' + id, { headers });
      }

      updaterh(rh: RH): Observable<any>{
        const params = JSON.stringify(rh);
        const headers = new HttpHeaders().set('Content-Type', 'application/json')
        .set('Authorization', this.getToken());
        return this._http.put(this.url + 'editarrh/'+ rh._id, params , {headers});
      }

}