import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { NO } from '../../../models/nivel_ocupacional';
import { NOService } from '../../../services/no.service'
import { DEService } from '../../../services/de.service';
@Component({
    selector: 'regno',
    templateUrl: './nivel_ocupacional.component.html',
    providers: [NOService, DEService]
})

export class RegisternoComponent {
    public title: String;
    public no: NO;
    public status: string;
    public status1: string;
    public editar: boolean;
    public alln_unica: boolean;
    public allc_unica: boolean;
    public alls: any[];
    public identity;
    constructor(
        private _route: ActivatedRoute,
        private _router: Router,
        private _noService: NOService,
        private _deService: DEService
    ) {
        this.title = 'Registro de Nivel Ocupacional';
        this.editar = false;
        this.initNO();
        this.initNOEdit();
        this.initall();
        this.identity = JSON.parse(localStorage.getItem('identity'));
        if (!this.identity) {
            this._router.navigate(['/home']);
          }
    }
    initall() {
        this._deService.getnoc().subscribe(
            response => {
                this.alls = response.no;
                console.log(this.alls);
            }
        );
    }
    allnUnica(nivel_ocupacional) {
        this.alln_unica = true;
        for (const alls of this.alls) {
            const allBD = alls.nivel_ocupacional;
             //console.log(allBD);
            if (allBD === nivel_ocupacional) {

                this.alln_unica = false;
            }
        }
    }
    allcUnica(codigo) {
        this.allc_unica = true;
        for (const alls of this.alls) {
            const allBD = alls.codigo;
            // console.log(cedulaBD);
            if (allBD === codigo) {

                this.allc_unica = false;
            }
        }
    }
    initNO() {
        this.no = new NO('',
            '',
            '',
            '',
            '',
            '',
            '');
    }
    initNOEdit() {
        this._route.params.subscribe(
            params => {
                console.log(params.id);
                if (params.id) {
                    this._noService.getnos(params.id).subscribe(
                        response => {
                            if (response.NO) {
                                this.no = response.NO;
                                this.editar = true;
                            }
                        }
                    )
                }
            }
        )
    }
    onSubmit(form) {
        if (this.editar == true) {
            this.onEdit(form);
        } else if (this.editar == false) {
            this.onNew(form);
        }
    }
    onEdit(form) {
        this._noService.updateno(this.no).subscribe(
            response => {
                if (response.NO && response.NO._id) {
                    this.status1 = 'success';
                    
                } else {
                    this.status1 = 'error';
                }
            },
            error => {
                console.log(<any>error);
            }

        );
    }
    onNew(form) {
        this._noService.register(this.no).subscribe(
            response => {
                if (response.NO && response.NO._id) {
                    // console.log(response.fun);
                    this.status = 'success';
                    form.reset();
                    this.initall();
                } else {
                    this.status = 'error';
                }
            },
            error => {
                console.log(<any>error);
            }
        );
    }
}