import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { Per } from '../../../models/personal';
import { PerService } from '../../../services/pers.service'
import { PAService } from '../../../services/pad.service';
import { PAD } from '../../../models/puesto_adicional';
import { NO } from '../../../models/nivel_ocupacional';
import { NOService } from '../../../services/no.service';
import { RL } from '../../../models/regimen_laboral';
import { RLService } from '../../../services/rl.service';
import { DP } from '../../../models/denominacion_puesto';
import { DPService } from '../../../services/dp.service';
import { ML } from '../../../models/modalidad_laboral';
import { MLService } from '../../../services/ml.service';
import { UO } from '../../../models/unidad_organica';
import { UOService } from '../../../services/uo.service';
import { EO } from '../../../models/escala_ocupacional';
import { EOService } from '../../../services/eo.service';
import { CN } from '../../../models/canton';
import { CNService } from '../../../services/can.service';
import Swal from 'sweetalert2';

@Component({
    selector: 'per-edit',
    templateUrl: './personal.component.html',
    providers: [PerService, PAService, NOService, RLService, DPService, MLService, UOService, EOService, CNService]
})
export class pereditComponent {
    public title: string;
    public per: Per;
    public status: string;
    public identity;
    public token;
    public num: boolean;
    public num1: boolean;

    public pa: PAD;
    public pas: PAD[] = [];
    public bufferpas: PAD[] = [];

    public no: NO;
    public nos: NO[] = [];
    public bufferno: NO[] = [];

    public rl: RL;
    public rls: RL[] = [];
    public bufferrl: RL[] = [];

    public dp: DP;
    public dps: DP[] = [];
    public bufferdp: DP[] = [];

    public ml: ML;
    public mls: ML[] = [];
    public bufferml: ML[] = [];

    public uo: UO;
    public uos: UO[] = [];
    public bufferuo: UO[] = [];

    public eo: EO;
    public eos: EO[] = [];
    public buffereo: EO[] = [];

    public cn: CN;
    public cns: CN[] = [];
    public buffercn: CN[] = [];
    constructor(
        private _route: ActivatedRoute,
        private _router: Router,
        private _perService: PerService,
        private _paService: PAService,
        private _noService: NOService,
        private _rlService: RLService,
        private _dpService: DPService,
        private _mlService: MLService,
        private _uoService: UOService,
        private _eoService: EOService,
        private _cnService: CNService

    ) {
        this.title = 'Actualizar datos';
        // this.fun = this._funService.getIdentity();
        this.identity = this.per;

        this.identity = JSON.parse(localStorage.getItem('identity'));
        if (!this.identity) {
            this._router.navigate(['/home']);
        }
        this.token = this._perService.getToken();
        this.initPer();
        this.initpas();
        this.initno();
        this.initrl();
        this.initdp();
        this.initml();
        this.inituo();
        this.initeo();
        this.initcn();
    }
    initPer() {
        this._route.params.subscribe(
            params => {
                const id = params.id;
                this._perService.findper(id).subscribe(
                    response => {
                        this.per = response.PERS;
                        console.log(this.per);
                    }
                );
            }
        );
    }
    ngOnInit() {
        console.log('per-edit cargado');
    }

    onSubmit() {
        console.log(this.per);
        this._perService.updateper(this.per).subscribe(
            response => {
                if (!response.per) {
                    this.status = 'error';
                } else {
                    this.status = 'success';
                }
            },
            error => {
                var errorMessage = <any>error;
                console.log(errorMessage);

                if (errorMessage != null) {
                    this.status = 'error';
                }
            }
        );
    }
    initpas() {
        this._paService.getpa().subscribe(
            response => {
                this.pas = response.PADI;
                this.bufferpas = response.PADI;
                console.log(this.pas);
            }, error => {
                console.error(error as any);
            }
        )
    }
    initno() {
        this._noService.getno().subscribe(
            response => {
                this.nos = response.NOC;
                this.bufferno = response.NOC;
                console.log(this.nos);
            }, error => {
                console.error(error as any);
            }
        )
    }
    initrl() {
        this._rlService.getrl().subscribe(
            response => {
                this.rls = response.RLA;
                this.bufferrl = response.RLA;
                console.log(this.rls);
            }, error => {
                console.error(error as any);
            }
        )
    }

    initdp() {
        this._dpService.getdp().subscribe(
            response => {
                this.dps = response.DDPU;
                this.bufferdp = response.DDPU;
                console.log(this.dps);
            }, error => {
                console.error(error as any);
            }
        )
    }

    initml() {
        this._mlService.getml().subscribe(
            response => {
                this.mls = response.MLA;
                this.bufferml = response.MLA;
                console.log(this.mls);
            }, error => {
                console.error(error as any);
            }
        )
    }

    inituo() {
        this._uoService.getuo().subscribe(
            response => {
                this.uos = response.UOR;
                this.bufferuo = response.UOR;
                console.log(this.uos);
            }, error => {
                console.error(error as any);
            }
        )
    }

    initeo() {
        this._eoService.geteo().subscribe(
            response => {
                this.eos = response.EOS;
                this.buffereo = response.EOS;
                console.log(this.eos);
            }, error => {
                console.error(error as any);
            }
        )
    }


    initcn() {
        this._cnService.getcn().subscribe(
            response => {
                this.cns = response.CAN;
                this.buffercn = response.CAN;
                console.log(this.cns);
            }, error => {
                console.error(error as any);
            }
        )
    }
    comprobarCadena(cadena:string) {
        const numeros = '0123456789';
        this.num = false;
        let hasNumbers: boolean = false;
        if (cadena && cadena.length) {
          for (const caracter of cadena) {
            if (numeros.indexOf(caracter) != -1) {
              hasNumbers = true;
              this.num = true;
            }
          }
        }
        if (hasNumbers == true) {
          Swal.fire({
            icon:'error',
            title: 'Datos Incorrectos',
            text: 'No se permiten numeros en este campo',
            timer: 2500
          })
        }
      }
      comprobarCadenan(cadena:string) {
        const numeros = '0123456789';
        this.num1 = false;
        let hasNumbers: boolean = false;
        if (cadena && cadena.length) {
          for (const caracter of cadena) {
            if (numeros.indexOf(caracter) != -1) {
              hasNumbers = true;
              this.num1 = true;
            }
          }
        }
        if (hasNumbers == true) {
          Swal.fire({
            icon:'error',
            title: 'Datos Incorrectos',
            text: 'No se permiten numeros en este campo',
            timer: 2500
          })
        }
      }
}