import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { RH } from '../../../models/recursos_humanos';
import { RHService } from '../../../services/rh.service';
import { FuncService } from '../../../services/fun.service';

@Component({
    selector: 'listrh',
    templateUrl: './recursos_humanos.component.html',
    providers: [RHService, FuncService]
})

export class ListarrhComponent {
    public title: String;
    public rh: RH;
    public Searchrhs: string;
    public rhs: RH[] = [];
    public bufferrh: RH[] = [];
    public identity;

    constructor(
        private _rhService: RHService,
        private _funcService: FuncService,
        private _router: Router
    ) {
        this.title = 'Listar Funcionarios de Recursos Humanos';
        this.initrh();
        this.identity = this._funcService.getIdentity();
        this.identity = JSON.parse(localStorage.getItem('identity'));
        if (!this.identity) {
            this._router.navigate(['/home']);
          }
    }

    initrh() {
        this._rhService.getrh().subscribe(
            response => {
                this.rhs = response.RHU;
                this.bufferrh = response.RHU;
                console.log(this.rhs);   
            }, error => {
                console.error(error as any);
            }
        )
    }
    cambioEstado(rh){
        let confirmacion = confirm('Seguro de que desea Cambiar el estado del funcionario de recursos humanos?');
        if (this.identity.role === 'admin')
        if (confirmacion === true) {
            if (rh.activa === true){
                rh.activa = false;
            } else {
                rh.activa = true;
            }
            this._rhService.cambiarEstado(rh).subscribe(
                response=> {
                    console.log(response);
                    
                }, error => {
                    console.log(error as any);
                    
                }
            );
        }
        
    }
    getData(event) {
        this.rhs = JSON.parse(event);

    }
    find(termino) {
        termino = termino.toLowerCase().replace(/ /g, '');
        this.rhs = [];
        if (this.bufferrh && this.bufferrh.length >=1 ) {
            for (const anc of this.bufferrh) {
                let datos = anc.nombre;
                if (anc.nombre) {
                    datos = datos + anc.nombre;
                }
                datos = datos.toLowerCase().replace(/ /g, '');
                datos = datos.toLowerCase().replace(/-/g, '');
                if ((datos.indexOf(termino)) >= 0) {
                    this.rhs.push(anc);
                    
                }
            }
        }
    }


}
