import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { ML } from '../../../models/modalidad_laboral';
import { MLService } from '../../../services/ml.service';
import { FuncService } from '../../../services/fun.service';

@Component({
    selector: 'listml',
    templateUrl: './modalidad_laboral.component.html',
    providers: [MLService, FuncService]
})

export class ListarmlComponent {
    public title: String;
    public ml: ML;
    public Searchml: string;
    public mls: ML[] = [];
    public bufferml: ML[] = [];
    public identity;

    constructor(
        private _mlService: MLService,
        private _funcService: FuncService,
        private _router: Router
    ) {
        this.title = 'Listar Modalidades Laborales';
        this.initml();
        this.identity = this._funcService.getIdentity();
        this.identity = JSON.parse(localStorage.getItem('identity'));
        if (!this.identity) {
            this._router.navigate(['/home']);
          }
    }

    initml() {
        this._mlService.getml().subscribe(
            response => {
                this.mls = response.MLA;
                this.bufferml = response.MLA;
                console.log(this.mls);   
            }, error => {
                console.error(error as any);
            }
        )
    }
    
    getData(event) {
        this.mls = JSON.parse(event);

    }
    find(termino) {
        termino = termino.toLowerCase().replace(/ /g, '');
        this.mls = [];
        if (this.bufferml && this.bufferml.length >=1 ) {
            for (const anc of this.bufferml) {
                let datos = anc.modalidad_laboral;
                if (anc.modalidad_laboral) {
                    datos = datos + anc.modalidad_laboral;
                }
                datos = datos.toLowerCase().replace(/ /g, '');
                datos = datos.toLowerCase().replace(/-/g, '');
                if ((datos.indexOf(termino)) >= 0) {
                    this.mls.push(anc);
                    
                }
            }
        }
    }


}