import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs';
import { global } from './global';
import { Func } from '../models/funcionario';

@Injectable()

export class FuncService {
    public url: string;
    public identity;
    public token;
    public rol;

    constructor(public _http: HttpClient) {
        this.url = global.url;
    }

    register(fun: Func): Observable<any> {
        let params = JSON.stringify(fun);
        let headers = new HttpHeaders().set('Content-Type', 'application/json');

        return this._http.post(this.url + 'registrarfun', params, { headers: headers })
    }

    signup(fun: any, gettoken = null): Observable<any> {
        if (gettoken != null) {
            fun.gettoken = gettoken;
        }
        let params = JSON.stringify(fun);
        let headers = new HttpHeaders().set('Content-Type', 'application/json');

        return this._http.post(this.url + 'login', params, { headers: headers });
    }
    getIdentity() {
        let identity = JSON.parse(localStorage.getItem('identity'));

        if (identity != 'undefined') {
            this.identity = identity;
        } else {
            this.identity = null;
        }

        return this.identity;
    }

    getToken() {
        let token = localStorage.getItem('token');

        if (token != 'undefined') {
            this.token = token;
        } else {
            this.token = null;
        }

        return this.token;
    }

    updateFun(fun: Func): Observable<any> {
        let params = JSON.stringify(fun);
        let headers = new HttpHeaders().set('Content-Type', 'application/json')
            .set('Authorization', this.getToken());

        return this._http.put(this.url + 'actualizarfun/' + fun._id, params, { headers: headers });

    }
    updateFuns(fun: Func): Observable<any> {
        let params = JSON.stringify(fun);
        let headers = new HttpHeaders().set('Content-Type', 'application/json')
            .set('Authorization', this.getToken());

        return this._http.put(this.url + 'actualizarfuns/' + fun._id, params, { headers: headers });

    }
    getfuns(): Observable<any> {
        const headers = new HttpHeaders().set('Content-Type', 'application/json')
            .set('Authorization', this.getToken());
        return this._http.get(this.url + 'listarfuns', { headers });
    }

    getfun(fun_id): Observable<any> {
        const headers = new HttpHeaders().set('Content-Type', 'application/json')
            .set('Authorization', this.getToken());

        return this._http.get(this.url + 'buscarfun/' + fun_id, { headers: headers });
    }

    cambiarEstado(funcionario: Func): Observable<any> {
        let params = JSON.stringify(funcionario);
        const headers = new HttpHeaders().set('Content-Type', 'application/json')
            .set('Authorization', this.getToken());
        console.log('cambiando estado....');

        return this._http.put(this.url + 'cambiar-estado', params, { headers });
    }
    cambiarrol(funcionario: Func): Observable<any> {
        let params = JSON.stringify(funcionario);
        const headers = new HttpHeaders().set('Content-Type', 'application/json')
            .set('Authorization', this.getToken());
        console.log('Esto se envia', funcionario);

        return this._http.put(this.url + 'cambiar-rol', params, { headers });
    }

    updatepass(id: string, funcionario): Observable<any> {
        let params = JSON.stringify(funcionario);
        let headers = new HttpHeaders().set('Content-Type', 'application/json')
            .set('Authorization', this.getToken());
        return this._http.put(this.url + 'cambiar-password/' + id, params, { headers: headers });

    }
    getpass(password): Observable<any> {
        this.getIdentity();
        let params = JSON.stringify({ password: password });
        let headers = new HttpHeaders().set('Content-Type', 'application/json')
            .set('Authorization', this.getToken());
        return this._http.post(this.url + 'comprobar-password/' + this.identity._id, params, { headers: headers });

    }
}