import {Component, OnInit} from '@angular/core';
import {Router, ActivatedRoute, Params} from '@angular/router';
import {EO} from '../../../models/escala_ocupacional';
import {EOService} from '../../../services/eo.service'
import { DEService } from '../../../services/de.service';
@Component({
    selector: 'regeo',
    templateUrl: './escala_ocupacional.component.html',
    providers: [EOService,DEService]
})

export class RegistereoComponent{
    public title: String;
    public eo: EO;
    public status: string;
    public status1: string;
    public editar: boolean;
    public alln_unica: boolean;
    public allc_unica: boolean;
    public alls: any[];
    public identity;
    constructor( 
        private _route: ActivatedRoute,
        private _router: Router,
        private _eoService : EOService,
        private _deService: DEService
    ){
        this.title = 'Registro de Escala Ocupacional';
        this.editar = false;
        this.initEO();
        this.initEOEdit();
        this.initall();
        this.identity = JSON.parse(localStorage.getItem('identity'));
        if (!this.identity) {
            this._router.navigate(['/home']);
          }
    }
    initall() {
        this._deService.geteso().subscribe(
            response => {
                this.alls = response.eo;
                console.log(this.alls);
            }
        );
    }
    allnUnica(escala_ocupacional) {
        this.alln_unica = true;
        for (const alls of this.alls) {
            const allBD = alls.escala_ocupacional;
             //console.log(allBD);
            if (allBD === escala_ocupacional) {

                this.alln_unica = false;
            }
        }
    }
    allcUnica(codigo) {
        this.allc_unica = true;
        for (const alls of this.alls) {
            const allBD = alls.codigo;
            // console.log(cedulaBD);
            if (allBD === codigo) {

                this.allc_unica = false;
            }
        }
    }
    initEO(){
        this.eo = new EO('',
        '',
        '',
        '',
        '',
        '',
        '');
    }
    initEOEdit() {
        this._route.params.subscribe(
            params => {
                console.log(params.id);
                if (params.id) {
                    this._eoService.geteos(params.id).subscribe(
                        response => {
                            if (response.EO) {
                                this.eo = response.EO;
                                this.editar = true;
                            }
                        }
                    )
                }
            }
        )
    }
    onSubmit(form) {
        if (this.editar == true) {
            this.onEdit(form);
        } else if (this.editar == false) {
            this.onNew(form);
        }
    }
    onEdit(form){
        this._eoService.updateeo(this.eo).subscribe(
            response => {
                if (response.EO && response.EO._id) {
                    this.status1 = 'success';
                    
                } else {
                    this.status1 = 'error';
                }
            },
            error => {
                console.log(<any>error);
            }
            
        );
    }
    onNew(form) {
        this._eoService.register(this.eo).subscribe(
            response => {
                if (response.EO && response.EO._id) {
                    // console.log(response.fun);
                    this.status = 'success';
                    form.reset();
                    this.initall();
                } else {
                    this.status = 'error';
                }
            },
            error => {
                console.log(<any>error);
            }
        );
    }
    }