import {Injectable} from '@angular/core';
import { HttpClient, HttpHeaders} from '@angular/common/http';
import { Observable } from 'rxjs';
import {global} from './global';
import {AP} from '../models/accion_personal';

@Injectable()

export class APService{
    public url: string;
    public identity;
    public token;
    public rol;

    constructor(public _http: HttpClient){
        this.url = global.url;
    }
    register(ap : AP): Observable<any>{
        let params = JSON.stringify(ap);
        let headers = new HttpHeaders().set('Content-Type', 'application/json')
                                       .set('Authorization', this.getToken());

        return this._http.post(this.url+ 'registrarap', params, {headers:headers})
    }
    update(ap : AP): Observable<any>{
        let params = JSON.stringify(ap);
        let headers = new HttpHeaders().set('Content-Type', 'application/json')
                                       .set('Authorization', this.getToken());
        return this._http.put(this.url+ 'editar-ap/' + ap._id, params, {headers:headers})
    }
    getIdentity(){
        let identity = JSON.parse(localStorage.getItem('identity'));

        if(identity != 'undefined'){
            this.identity = identity;
        }else{
            this.identity = null;
        }

        return this.identity;
    }
    getAp(id_ap): Observable<any> {
        let headers = new HttpHeaders().set('Content-Type', 'application/json')
                                       .set('Authorization', this.getToken());

        return this._http.get(this.url + 'buscarap/' + id_ap, {headers:headers})
    }
    getAps(): Observable<any> {
        let headers = new HttpHeaders().set('Content-Type', 'application/json')
                                       .set('Authorization', this.getToken());

        return this._http.get(this.url + 'listaraps', {headers:headers})
    }
    getToken(){
        let token = localStorage.getItem('token');

        if(token != 'undefined'){
            this.token = token;
        }else{
            this.token = null;
        }

        return this.token;
    }
    cambiarEstado(accion: AP): Observable<any> {
        let params = JSON.stringify(accion);
        const headers = new HttpHeaders().set('Content-Type', 'application/json')
                                         .set('Authorization', this.getToken());
        console.log('cambiando estado....');
        
        return this._http.put(this.url + 'estadoap', params , {headers});
    }
    getCodigos(): Observable<any> {
        const headers = new HttpHeaders().set('Content-Type', 'application/json');
        return this._http.get(this.url + 'get-codigos' , {headers});
    }
    // Estadisticas
    getPorTipo(tipo: string): Observable<any> {
        const headers = new HttpHeaders().set('Content-Type', 'application/json');
        return this._http.get(this.url + 'get-contador-tipo/' + tipo , {headers});
    }
    getPorActiva(activa: boolean): Observable<any> {
        const headers = new HttpHeaders().set('Content-Type', 'application/json');
        return this._http.get(this.url + 'get-contador-activa/' + activa , {headers});
    }
}