import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { Func } from '../../models/funcionario';
import { FuncService } from '../../services/fun.service';

@Component({
  selector: 'home',
  templateUrl: './home.component.html',
  providers: [FuncService]
})
export class HomeComponent implements OnInit{
  public title: string;
  public title1: string;
  public logo: string;
  public identity;
  constructor(
    private _route: ActivatedRoute,
    private _router: Router,
    private _funService: FuncService,

  ) {
    this.logo = '../../imagenes/salud.png';
    this.title = 'Bienvenid@ a tu sistema de gestion de Acciones de Personal';
    this.title1 = 'DISTRITO DE SALUD';
    this.identity = JSON.parse(localStorage.getItem('identity'));

    if (!this.identity) {
      this._router.navigate(['/login']);
      // location.reload();
    }
  }
  ngOnInit() {
    console.log('home cargado');
  }

}