import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { Func } from '../../../models/funcionario';
import { FuncService } from '../../../services/fun.service'
import { Subscriber } from 'rxjs';
import Swal from 'sweetalert2';
@Component({
    selector: 'listfun',
    templateUrl: './listfun.component.html',
    providers: [FuncService]
})

export class ListarfunComponent {
    public title: String;
    public fun: Func;
    public Searchfuns: string;
    public funs: Func[] = [];
    public bufferFuns: Func[] = [];
    public identity;
    public fecha = new Date();
    public ano;

    constructor(
        private _funcService: FuncService,
        private _router: Router
    ) {
        this.title = 'Listar Funcionarios';
        this.initFuns();
        this.identity = this._funcService.getIdentity();
        this.ano = this.fecha.getFullYear();

        this.identity = JSON.parse(localStorage.getItem('identity'));
        if (!this.identity || this.identity.role === 'ROLE_USER') {
            this._router.navigate(['/home']);
        }
    }

    initFuns() {
        this._funcService.getfuns().subscribe(
            response => {
                this.funs = response.FUNC;
                this.bufferFuns = response.FUNC;
                console.log(this.funs);
            }, error => {
                console.error(error as any);
            }
        )
    }
    cambioEstado(funcionario) {
        Swal.fire({
            title: 'Desea cambiar el estado de este usuario?',
            text: "Se cambiara el estado del usuario elegido",
            icon: 'warning',
            showCancelButton: true,
            confirmButtonColor: '#3085d6',
            cancelButtonColor: '#d33',
            confirmButtonText: 'Si, Cambiar!'
        }).then((result) => {
            if (result.value) {
                Swal.fire(
                    'ESTADO CAMBIADO!',
                    'El estado de este usuario ha sido cambiado',
                    'success'
                )
                if (funcionario.activa === true) {
                    funcionario.activa = false;
                } else {
                    funcionario.activa = true;
                }
                this._funcService.cambiarEstado(funcionario).subscribe(
                    response => {
                        console.log(response);

                    }, error => {
                        console.log(error as any);

                    }
                    );
                }
            })


        }
    cambiorol(funcionario) {
        Swal.fire({
            title: 'Desea cambiar de rol a este usuario?',
            text: "Se cambiara de rol al usuario elegido",
            icon: 'warning',
            showCancelButton: true,
            confirmButtonColor: '#3085d6',
            cancelButtonColor: '#d33',
            confirmButtonText: 'Si, Cambiar!'
        }).then((result) => {
            if (result.value) {
                Swal.fire(
                    'ROL CAMBIADO!',
                    'El rol de este usuario ha sido cambiado',
                    'success'
                )
                if (funcionario.role === 'admin') {
                    funcionario.role = 'ROLE_USER';
                } else {
                    funcionario.role = 'admin';
                }
                this._funcService.cambiarrol(funcionario).subscribe(
                    response => {
                        console.log(response);
                        console.log('este es el cambio:', funcionario);


                    }, error => {
                        console.log(error as any);

                    }
                    );
                }
            })


        }

    getData(event) {
        this.funs = JSON.parse(event);

    }
    find(termino) {
        termino = termino.toLowerCase().replace(/ /g, '');
        this.funs = [];
        if (this.bufferFuns && this.bufferFuns.length >= 1) {
            for (const func of this.bufferFuns) {
                let datos = func.nombres + func.apellidos;
                if (func.usuario) {
                    datos = datos + func.usuario;
                }
                datos = datos.toLowerCase().replace(/ /g, '');
                datos = datos.toLowerCase().replace(/-/g, '');
                if ((datos.indexOf(termino)) >= 0) {
                    this.funs.push(func);

                }
            }
        }
    }
}
