import {Component, OnInit} from '@angular/core';
import {Router, ActivatedRoute, Params} from '@angular/router';
import {RL} from '../../../models/regimen_laboral';
import {RLService} from '../../../services/rl.service'
import { DEService } from '../../../services/de.service';
@Component({
    selector: 'regrl',
    templateUrl: './regimen_laboral.component.html',
    providers: [RLService, DEService]
})

export class RegisterrlComponent{
    public title: String;
    public rl: RL;
    public status: string;
    public status1: string;
    public editar: boolean;
    public alln_unica: boolean;
    public allc_unica: boolean;
    public alls: any[];
    public identity;
    constructor( 
        private _route: ActivatedRoute,
        private _router: Router,
        private _rlService : RLService,
        private _deService: DEService
    ){
        this.title = 'Registro de Régimen Laboral';
        this.editar = false;
        this.initRL();
        this.initRLEdit();
        this.initall();
        this.identity = JSON.parse(localStorage.getItem('identity'));
        if (!this.identity) {
            this._router.navigate(['/home']);
          }
    }
    initall() {
        this._deService.getrla().subscribe(
            response => {
                this.alls = response.rl;
                console.log(this.alls);
            }
        );
    }
    allnUnica(regimen_laboral) {
        this.alln_unica = true;
        for (const alls of this.alls) {
            const allBD = alls.regimen_laboral;
             //console.log(allBD);
            if (allBD === regimen_laboral) {

                this.alln_unica = false;
            }
        }
    }
    allcUnica(codigo) {
        this.allc_unica = true;
        for (const alls of this.alls) {
            const allBD = alls.codigo;
            // console.log(cedulaBD);
            if (allBD === codigo) {

                this.allc_unica = false;
            }
        }
    }
    initRL(){
        this.rl = new RL('',
        '',
        '',
        '',
        '',
        '',
        '');
    }
    initRLEdit() {
        this._route.params.subscribe(
            params => {
                console.log(params.id);
                if (params.id) {
                    this._rlService.getrls(params.id).subscribe(
                        response => {
                            if (response.RL) {
                                this.rl = response.RL;
                                this.editar = true;
                            }
                        }
                    )
                }
            }
        )
    }
    onSubmit(form) {
        if (this.editar == true) {
            this.onEdit(form);
        } else if (this.editar == false) {
            this.onNew(form);
        }
    }
    onEdit(form){
        this._rlService.updaterl(this.rl).subscribe(
            response => {
                if (response.RL && response.RL._id) {
                    this.status1 = 'success';
                    
                } else {
                    this.status1 = 'error';
                }
            },
            error => {
                console.log(<any>error);
            }
            
        );
    }
    onNew(form) {
        this._rlService.register(this.rl).subscribe(
            response => {
                if (response.RL && response.RL._id) {
                    // console.log(response.fun);
                    this.status = 'success';
                    form.reset();
                    this.initall();
                } else {
                    this.status = 'error';
                }
            },
            error => {
                console.log(<any>error);
            }
        );
    }
    }