import {Component, OnInit} from '@angular/core';
import {Router, ActivatedRoute, Params} from '@angular/router';
import {RH} from '../../../models/recursos_humanos';
import {RHService} from '../../../services/rh.service';
import {DEService} from '../../../services/de.service';

@Component({
    selector: 'regrh',
    templateUrl: './recursos_humanos.component.html',
    providers: [RHService, DEService]
})

export class RegisterrhComponent{
    public title: String;
    public rh: RH;
    public status: string;
    public status1: string;
    public editar: boolean;
    public rh_unica: boolean;
    public identity;
    public rhs: any[];
    constructor( 
        private _route: ActivatedRoute,
        private _router: Router,
        private _rhService : RHService,
        private _deService: DEService
    ){
        this.title = 'Registro de Recursos Humanos';
        this.editar = false;
        this.initRH();
        this.initRHEdit();
        this.initrhd();
        this.identity = JSON.parse(localStorage.getItem('identity'));
        if (!this.identity) {
            this._router.navigate(['/home']);
          }
    }
    initrhd() {
        this._deService.getrhm().subscribe(
          response => {
            this.rhs = response.rhs;
            console.log(this.rhs);
          }
        );
      }
      rhUnica(nombre) {
        this.rh_unica = true;
        for (const rhs of this.rhs) {
            const rhBD = rhs.nombre;
            // console.log(cedulaBD);
            if (rhBD === nombre) {
    
              this.rh_unica = false;
            }
        }
      }
    initRH() {
        this.rh = {
            _id: '',
            nombre: '',
            activa: true,
            autor: '',
            fecha_autor: '',
            editor: '',
            fecha_editor : ''
        };
    }
    initRHEdit() {
        this._route.params.subscribe(
            params => {
                console.log(params.id);
                if (params.id) {
                    this._rhService.getrhs(params.id).subscribe(
                        response => {
                            if (response.RH) {
                                this.rh = response.RH;
                                this.editar = true;
                            }
                        }
                    )
                }
            }
        )
    }
    onSubmit(form) {
        if (this.editar == true) {
            this.onEdit(form);
        } else if (this.editar == false) {
            this.onNew(form);
        }
    }
    onEdit(form){
        
        this._rhService.updaterh(this.rh).subscribe(
            response => {
                if (response.RH && response.RH._id) {
                    // console.log(response.fun);
                    this.status1 = 'success';
                    
                } else {
                    this.status1 = 'error';
                }
            },
            error => {
                console.log(<any>error);
            }
            
        );
    }
    onNew(form) {
        this._rhService.register(this.rh).subscribe(
            response => {
                if (response.RH && response.RH._id) {
                    // console.log(response.fun);
                    this.status = 'success';
                    form.reset();
                } else {
                    this.status = 'error';
                }
            },
            error => {
                console.log(<any>error);
            }
        );
    }
    }



