import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { Func } from '../../../models/funcionario';
import { FuncService } from '../../../services/fun.service';
import { DEService } from '../../../services/de.service';
import Swal from 'sweetalert2';

@Component({
  selector: 'regfun',
  templateUrl: './regfun.component.html',
  providers: [FuncService, DEService]
})

export class RegisterfunComponent implements OnInit {
  public title: string;
  public fun: Func;
  public status: string;
  public ci_correcta: boolean;
  public ci_unica: boolean;
  public correo_unica: boolean;
  public usuario_unica: boolean;
  public cedulas: any[];
  public identity;
  public num: boolean;
  public num1: boolean;
  constructor(
    private _route: ActivatedRoute,
    private _router: Router,
    private _funService: FuncService,
    private _deService: DEService
  ) {
    this.title = 'Registro de Funcionarios';
    this.fun = new Func('',
      '',
      '',
      '',
      '',
      '',
      '',
      '',
      '',
      '',
      '',
      true);
    this.initCedulas();
    this.identity = JSON.parse(localStorage.getItem('identity'));
    if (!this.identity) {
        this._router.navigate(['/home']);
      }
    // this.initcorreos();
    // this.initUsu();
  }
  cedulaUnica(cedula) {
    this.ci_unica = true;
    for (const cedulas of this.cedulas) {
        const cedulaBD = cedulas.ci_fun;
        // console.log(cedulaBD);
        if (cedulaBD == cedula) {

          this.ci_unica = false;
        }
    }
  }
  correoUnica(correo) {
    this.correo_unica = true;
    for (const correos of this.cedulas) {
        const correoBD = correos.correo;
        // console.log(correoBD);
        if (correoBD == correo) {

          this.correo_unica = false;
        }
    }
  }
  usuarioUnica(usuario) {
    this.usuario_unica = true;
    for (const usuarios of this.cedulas) {
        const usuarioBD = usuarios.usuario;
        // console.log(usuarioBD);
        if (usuarioBD == usuario) {
          this.usuario_unica = false;
        }
    }
  }
  comprobar_ci(cedula) {
    this.ci_correcta = false;
    if (cedula.length === 10) {
      let cedulaCache: any[];
      let sumaPares = 0;
      let sumaImpares = 0;
      let sumaTotal: number;
      let verificador: number;
      let comparador = 10;
      cedulaCache = cedula.split('');
      console.log(cedulaCache);

      for (let i = 0; i < 9; i = i + 2) {
        let impar = cedulaCache[i];
        impar = impar * 2;
        if (impar >= 10) {
          impar = impar - 9;
        }
        sumaImpares = sumaImpares + impar;

      }
      for (let i = 1; i < 8; i = i + 2) {
        const par = cedulaCache[i];
        sumaPares = sumaPares + parseInt(par, 0);
      }
      console.log(sumaPares);
      sumaTotal = sumaPares + sumaImpares;

      while (comparador < sumaTotal) {
        comparador = comparador + 10;
      }
      if (comparador - sumaTotal <= 9) {
        verificador = comparador - sumaTotal;
        console.log(verificador, ' = ', cedulaCache[9]);

        if (verificador == cedulaCache[9]) {
          this.ci_correcta = true;
        } else {
          this.ci_correcta = false;
        }
      }
    }
  }
  ngOnInit() {
    console.log('Componente de registro de funcionarios cargado.....');
  }
  initCedulas() {
    this._deService.getCedulas().subscribe(
      response => {
        this.cedulas = response.cedulas;
        console.log(this.cedulas);
      }
    );
  }
  // initcorreos() {
  //   this._deService.getcorreos().subscribe(
  //     response => {
  //       this.correos = response.correo;
  //       console.log(this.correos);
  //     }
  //   );
  // }
  // initUsu() {
  //   this._deService.getusu().subscribe(
  //     response => {
  //       this.usuarios = response.usuario;
  //       console.log(this.usuarios);
  //     }
  //   )
  // }

  onSubmit(form) {
    this._funService.register(this.fun).subscribe(
      response => {
        if (response.FUN && response.FUN._id) {
          // console.log(response.fun);

          this.status = 'success';
          form.reset();
        } else {
          this.status = 'error';
        }
      },
      error => {
        console.log(error as any);
      }
    );
  }

  comprobarCadena(cadena:string) {
    this.num = false
    const numeros = '0123456789';
    let hasNumbers: boolean = false;
    if (cadena && cadena.length) {
      for (const caracter of cadena) {
        if (numeros.indexOf(caracter) != -1) {
          hasNumbers = true;
          this.num = true;
        }
      }
    }
    if (hasNumbers == true) {
      Swal.fire({
        icon:'error',
        title: 'Datos Incorrectos',
        text: 'No se permiten numeros en este campo',
        timer: 2500
      })
    }
  }
  comprobarCadenan(cadena:string) {
    const numeros = '0123456789';
    this.num1 = false;
    let hasNumbers: boolean = false;
    if (cadena && cadena.length) {
      for (const caracter of cadena) {
        if (numeros.indexOf(caracter) != -1) {
          hasNumbers = true;
          this.num1 = true;
        }
      }
    }
    if (hasNumbers == true) {
      Swal.fire({
        icon:'error',
        title: 'Datos Incorrectos',
        text: 'No se permiten numeros en este campo',
        timer: 2500
      })
    }
  }

}

