// tslint:disable: variable-name
// tslint:disable: member-ordering

import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { Per } from '../../../../models/personal';
import { PerService } from '../../../../services/pers.service';
import { Subscriber } from 'rxjs';
import { FuncService } from '../../../../services/fun.service';
import { Func } from '../../../../models/funcionario';
import { CO } from '../../../../models/codigo';
import { COService } from '../../../../services/co.service';
import { APService } from '../../../../services/ap.service';
import Swal from 'sweetalert2';

@Component({
    selector: 'asignar',
    templateUrl: './personal.component.html',
    providers: [APService, PerService, FuncService, COService],
})

export class asignarComponent {
    public title: String;
    public per: Per;
    public Searchper: string;
    public pers;
    public bufferpers: Per[] = [];
    public identity;
    public fecha = new Date();
    public ano;
    public funcs: Func;
    public asignacion;
    public memorandum;
    public noAsignado: boolean;
    public co;
    public codigo;
    public memo: boolean;
    public memom;
    constructor(
        private _percService: PerService,
        private _funcService: FuncService,
        private _router: Router,
        private _coService: COService,
        private _apService: APService
    ) {
        this.title = 'Asignar Personal';
        this.initper();
        this.identity = this._percService.getIdentity();
        this.identity = JSON.parse(localStorage.getItem('identity'));
        if (!this.identity || this.identity.role === 'ROLE_USER') {
            this._router.navigate(['/home']);
        }
        this.ano = this.fecha.getFullYear();
        this.initFunc();
        this.initCodigo();
        this.memo = false;
    }
    initCodigo() {
        this._coService.getco().subscribe(
            response => {
                this.co = response.COD[0];
                this.codigo = this.co.codigo;
            }
        );
    }
    imprimir() {
        window.print();
    }
    initFunc() {
        this._funcService.getfuns().subscribe(
            response => {
                this.funcs = response.FUNC;
                console.log(this.funcs);

            }
        );
    }
    initper() {
        this.pers = [];
        this.bufferpers = [];
        this._percService.getpers().subscribe(
            response => {
                for (const per of response.PERS) {
                    if (per.activa === true) {
                        this.pers.push(per);
                        this.bufferpers.push(per);
                    }
                }
                console.log(this.pers);
            }, error => {
                console.error(error as any);
            }
        );
    }

    getData(event) {
        this.pers = JSON.parse(event);

    }
    find(termino) {
        termino = termino.toLowerCase().replace(/ /g, '');
        // termino = termino.split(';');
        // console.log(termino);
        this.pers = [];
        if (this.bufferpers && this.bufferpers.length >= 1) {
            for (const per of this.bufferpers) {
                let datos = per.nombres + per.apellidos + per.canton + per.cedula;
                if (per.nombres) {
                    datos = datos + per.nombres;
                }
                datos = datos.toLowerCase().replace(/ /g, '');
                datos = datos.toLowerCase().replace(/-/g, '');
                if ((datos.indexOf(termino)) >= 0) {
                    this.pers.push(per);
                }
            }
        }
    }
    getPersonal(personal) {
        this.asignacion = JSON.parse(JSON.stringify(personal));
        this.getAPsPer(this.asignacion.cedula);
    }
    public APsPer = [];
    public codAPList;
    getAPsPer(cedula) {
        this.APsPer = [];
        this._apService.getAps().subscribe(
            response => {
                if (response.aps) {
                    for (const ap of response.aps) {
                        if (ap.ci_per == cedula) {
                            this.APsPer.push(ap);
                        }
                    }
                }
            }
        );
    }
    // comparar quipux
    quipuxCompare(quipux: string) {
        
        for (const ap of this.APsPer) {
            console.log('ap:', ap.quipux, 'quipux: ', quipux);
            if (ap.quipux.toLowerCase() == quipux.toLowerCase()) {
                this.memo = true;
                this.memom = "";
                Swal.fire({
                    icon: 'error',
                    title: 'Còdigo repetido',
                    text: 'Error, el Código de Memorandum ya se ingreso anteriormente!',
                  })
            }
        }
    }
    incrCodigo(): string {
        this.codAPList = [];
        let nomeActual = this.codigo.split(/-/gi, 3);
        let newCod = parseInt(nomeActual[0]);
        nomeActual = nomeActual[1];
        this._apService.getCodigos().subscribe(
            response => {
                for (const codigo of response.Codigos) {
                    let nomenclatura = codigo.codigo_ap.split(/-/gi, 3);
                    nomenclatura = nomenclatura[1];
                    if (nomeActual == nomenclatura) {
                        this.codAPList.push(codigo.codigo_ap);
                    }
                }
            }
        );
        for (const personal of this.pers) {
            if (personal.codigoAP) {
                let nomenAP = personal.codigoAP.split(/-/gi, 3);
                nomenAP = nomenAP[1];
                console.log('comparando nomen actual: ', nomeActual, ' nomen AP: ', nomenAP);
                if (nomeActual == nomenAP) {
                    this.codAPList.push(personal.codigoAP);
                }
            }
        }
        let repetido = true;
        this.codAPList.sort();
        console.log(this.codAPList);
        // newCod++
        if (this.codAPList.length <= 0 && this.codigo === '000') {
            newCod ++;
        } else if (this.codAPList.length >= 1) {
            while (repetido == true) {
                for (const codigos of this.codAPList) {
                    let codigo = codigos.split(/-/, 3);
                    codigo = parseInt(codigo[0]);
                    console.log('codigo: ', codigo, ' newCod: ', newCod);
                    
                    if (codigo != newCod) repetido = false;
                    if (repetido == true) newCod++;
                    console.log('repetido?: ', repetido);
                    
                }
            }
        }
        let newCodString = newCod.toString();
        while (newCodString.length < 3) {
            newCodString = '0' + newCodString;
        }
        console.log('codigo final: ', newCodString);
        
        const codigoArray = this.codigo.split(/-/gi, 3);
        newCodString = newCodString + '-' + codigoArray[1] + '-' + codigoArray[2];
        // const codigoArray = this.codigo.split(/-/gi, 3);
        // codigoArray[0] = parseInt(codigoArray[0]) + 1;
        // codigoArray[0] = codigoArray[0].toString();
        // 
        // 
        // }
        // newCod = codigoArray[0] + '-' + codigoArray[1] + '-' + codigoArray[2];
        // console.log(this.codigo);
        this.co.codigo = newCodString;
        return newCodString;
    }

    addFunc(asignacion, memorandum) {
        this.asignacion.asignado = asignacion;
        this.asignacion.memorandum = memorandum;
        // this.incrCodigo();
        this.asignacion.codigoAP = this.incrCodigo();
        this.updatePersonal(this.asignacion);
    }
    removeFunc() {
        this.asignacion.asignado = null;
        this.asignacion.memorandum = null;
        this.asignacion.codigoAP = null;
        this.updatePersonal(this.asignacion);
    }

    updatePersonal(personal) {
        this._percService.updateper(personal).subscribe(
            response => {
                console.log(response);
                this.initper();
                this.asignacion = null;
            }
        );
    }

}
