import {Injectable} from '@angular/core';
import { HttpClient, HttpHeaders} from '@angular/common/http';
import { Observable } from 'rxjs';
import {global} from './global';
import {CO} from '../models/codigo';

@Injectable()

export class COService{
    public url: string;
    public identity;
    public token;
    public rol;

    constructor(public _http: HttpClient){
        this.url = global.url;
        this.getToken();
    }

    getToken() {
        let token = localStorage.getItem('token');

        if (token != 'undefined') {
            this.token = token;
        } else {
            this.token = null;
        }
        
        return this.token;
    }
    register(co: CO): Observable<any>{
        let params = JSON.stringify(co);
        let headers = new HttpHeaders().set('Content-Type', 'application/json')
                                       .set('Authorization', this.getToken());

        return this._http.post(this.url + 'registrarco', params, {headers})
    }

    getIdentity(){
        let identity = JSON.parse(localStorage.getItem('identity'));

        if(identity != 'undefined'){
            this.identity = identity;
        }else{
            this.identity = null;
        }

        return this.identity;
    }
    
    getco(): Observable<any> {
        const headers = new HttpHeaders().set('Content-Type', 'application/json')
          .set('Authorization', this.getToken());
        return this._http.get(this.url + 'listarco', { headers });
      }
      updateco(co: CO): Observable<any>{
        const params = JSON.stringify(co);
        const headers = new HttpHeaders().set('Content-Type', 'application/json')
        .set('Authorization', this.getToken());
        return this._http.put(this.url + 'editarco/'+ co._id, params , {headers});
      }

}