import {Component, OnInit} from '@angular/core';
import {Router, ActivatedRoute, Params} from '@angular/router';
import {RR} from '../../../models/responsable_registro';
import {RRService} from '../../../services/rr.service';
import { DEService } from '../../../services/de.service';

@Component({
    selector: 'regrr',
    templateUrl: './responsable_registro.component.html',
    providers: [RRService,DEService]
})

export class RegisterrrComponent{
    public title: String;
    public rr: RR;
    public status: string;
    public status1: string;
    public editar: boolean;
    public rr_unica: boolean;
    public rrs: any[];
    public identity;
    constructor( 
        private _route: ActivatedRoute,
        private _router: Router,
        private _rrService : RRService,
        private _deService: DEService
    ){
        this.title = 'Registro de Responsable del Registro';
        this.editar = false;
        this.initRR();
        this.initRREdit();
        this.initrrd();
        this.identity = JSON.parse(localStorage.getItem('identity'));
        if (!this.identity) {
            this._router.navigate(['/home']);
          }
    }
    initrrd() {
        this._deService.getrrg().subscribe(
          response => {
            this.rrs = response.rrs;
            console.log(this.rrs);
          }
        );
      }
      rrUnica(nombre) {
        this.rr_unica = true;
        for (const rrs of this.rrs) {
            const rrBD = rrs.nombre;
            // console.log(cedulaBD);
            if (rrBD === nombre) {
    
              this.rr_unica = false;
            }
        }
      }
    initRR() {
        this.rr = {
            _id: '',
            nombre: '',
            activa: true,
            autor: '',
            fecha_autor: '',
            editor: '',
            fecha_editor : ''
        };
    }
    initRREdit() {
        this._route.params.subscribe(
            params => {
                console.log(params.id);
                if (params.id) {
                    this._rrService.getrrs(params.id).subscribe(
                        response => {
                            if (response.RR) {
                                this.rr = response.RR;
                                this.editar = true;
                            }
                        }
                    )
                }
            }
        )
    }
    onSubmit(form) {
        if (this.editar == true) {
            this.onEdit(form);
        } else if (this.editar == false) {
            this.onNew(form);
        }
    }
    onEdit(form){
        
        this._rrService.updaterr(this.rr).subscribe(
            response => {
                if (response.RR && response.RR._id) {
                    // console.log(response.fun);
                    this.status1 = 'success';
                    
                } else {
                    this.status1 = 'error';
                }
            },
            error => {
                console.log(<any>error);
            }
            
        );
    }
    onNew(form) {
        this._rrService.register(this.rr).subscribe(
            response => {
                if (response.RR && response.RR._id) {
                    // console.log(response.fun);
                    this.status = 'success';
                    form.reset();
                } else {
                    this.status = 'error';
                }
            },
            error => {
                console.log(<any>error);
            }
        );
    }
    }


