import {Component, OnInit} from '@angular/core';
import {Router, ActivatedRoute, Params} from '@angular/router';
import {DP} from '../../../models/denominacion_puesto';
import {DPService} from '../../../services/dp.service';
import { DEService } from '../../../services/de.service';

@Component({
    selector: 'regdp',
    templateUrl: './denominacion_puesto.component.html',
    providers: [DPService, DEService]
})

export class RegisterdpComponent{
    public title: String;
    public dp: DP;
    public status: string;
    public status1: string;
    public editar: boolean;
    public alln_unica: boolean;
    public allc_unica: boolean;
    public identity;
    public alls: any[];
    constructor( 
        private _route: ActivatedRoute,
        private _router: Router,
        private _dpService : DPService,
        private _deService: DEService
    ){
        this.title = 'Registro de Denominación de Puesto';
        this.editar = false;
        this.initDP();
        this.initDPEdit();
        this.initall();
        this.identity = JSON.parse(localStorage.getItem('identity'));
        if (!this.identity) {
            this._router.navigate(['/home']);
          }
    }
    initall() {
        this._deService.getddp().subscribe(
            response => {
                this.alls = response.dp;
                console.log(this.alls);
            }
        );
    }
    allnUnica(denominacion_puesto) {
        this.alln_unica = true;
        for (const alls of this.alls) {
            const allBD = alls.denominacion_puesto;
             //console.log(allBD);
            if (allBD === denominacion_puesto) {

                this.alln_unica = false;
            }
        }
    }
    allcUnica(codigo) {
        this.allc_unica = true;
        for (const alls of this.alls) {
            const allBD = alls.codigo;
            // console.log(cedulaBD);
            if (allBD === codigo) {

                this.allc_unica = false;
            }
        }
    }
    initDP(){
        this.dp = new DP('',
        '',
        '',
        '',
        '',
        '',
        '');
    }
    initDPEdit() {
        this._route.params.subscribe(
            params => {
                console.log(params.id);
                if (params.id) {
                    this._dpService.getdps(params.id).subscribe(
                        response => {
                            if (response.DDP) {
                                this.dp = response.DDP;
                                this.editar = true;
                            }
                        }
                    )
                }
            }
        )
    }
    onSubmit(form) {
        if (this.editar == true) {
            this.onEdit(form);
        } else if (this.editar == false) {
            this.onNew(form);
        }
    }
    onEdit(form){
        this._dpService.updatedp(this.dp).subscribe(
            response => {
                if (response.DDP && response.DDP._id) {
                    this.status1 = 'success';
                    
                } else {
                    this.status1 = 'error';
                }
            },
            error => {
                console.log(<any>error);
            }
            
        );
    }
    onNew(form) {
        this._dpService.register(this.dp).subscribe(
            response => {
                if (response.DP && response.DP._id) {
                    // console.log(response.fun);
                    this.status = 'success';
                    form.reset();
                    this.initall();
                } else {
                    this.status = 'error';
                }
            },
            error => {
                console.log(<any>error);
            }
        );
    }
    }