import {Component, OnInit} from '@angular/core';
import {Router, ActivatedRoute, Params} from '@angular/router';
import {PAD} from '../../../models/puesto_adicional';
import {PAService} from '../../../services/pad.service'
import { Message } from '@angular/compiler/src/i18n/i18n_ast';
import { DEService } from '../../../services/de.service';

@Component({
    selector: 'regpa',
    templateUrl: './puesto_adicional.component.html',
    providers: [PAService, DEService]
})

export class RegisterpaComponent{
    public title: String;
    public pa: PAD;
    public status: string;
    public status1: string;
    public editar: boolean;
    public alln_unica: boolean;
    public allc_unica: boolean;
    public identity;
    public alls: any[];
    constructor( 
        private _route: ActivatedRoute,
        private _router: Router,
        private _paService : PAService,
        private _deService: DEService
    ){
        this.title = 'Registro de Puesto Adicional';
        this.editar = false;
        this.initPA();
        this.initPAEdit();
        this.initall();
        this.identity = JSON.parse(localStorage.getItem('identity'));
        if (!this.identity) {
            this._router.navigate(['/home']);
          }
    }
    initall() {
        this._deService.getpadi().subscribe(
            response => {
                this.alls = response.pad;
                console.log(this.alls);
            }
        );
    }
    allnUnica(puesto_adicional) {
        this.alln_unica = true;
        for (const alls of this.alls) {
            const allBD = alls.puesto_adicional;
             //console.log(allBD);
            if (allBD === puesto_adicional) {

                this.alln_unica = false;
            }
        }
    }
    allcUnica(codigo) {
        this.allc_unica = true;
        for (const alls of this.alls) {
            const allBD = alls.codigo;
            // console.log(cedulaBD);
            if (allBD === codigo) {

                this.allc_unica = false;
            }
        }
    }
    initPA(){
        this.pa = new PAD('',
        '',
        '',
        '',
        '',
        '',
        '');
    }
    initPAEdit() {
        this._route.params.subscribe(
            params => {
                console.log(params.id);
                if (params.id) {
                    this._paService.getpas(params.id).subscribe(
                        response => {
                            if (response.PAD) {
                                this.pa = response.PAD;
                                this.editar = true;
                            }
                        }
                    )
                }
            }
        )
    }
    onSubmit(form) {
        if (this.editar == true) {
            this.onEdit(form);
        } else if (this.editar == false) {
            this.onNew(form);
        }
    }
    onEdit(form){
        this._paService.updatepa(this.pa).subscribe(
            response => {
                if (response.PAD && response.PAD._id) {
                    this.status1 = 'success';
                    
                } else {
                    this.status1 = 'error';
                }
            },
            error => {
                console.log(<any>error);
            }
            
        );
    }
    onNew(form) {
        this._paService.register(this.pa).subscribe(
            response => {
                if (response.PAD && response.PAD._id) {
                    // console.log(response.fun);
                    this.status = 'success';
                    form.reset();
                    this.initall();
                } else {
                    this.status = 'error';
                }
            },
            error => {
                console.log(<any>error);
            }
        );
    }
    }