import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { Per } from '../../../models/personal';
import { PerService } from '../../../services/pers.service'
import { PAService } from '../../../services/pad.service';
import { PAD } from '../../../models/puesto_adicional';
import { NO } from '../../../models/nivel_ocupacional';
import { NOService } from '../../../services/no.service';
import { RL } from '../../../models/regimen_laboral';
import { RLService } from '../../../services/rl.service';
import { DP } from '../../../models/denominacion_puesto';
import { DPService } from '../../../services/dp.service';
import { ML } from '../../../models/modalidad_laboral';
import { MLService } from '../../../services/ml.service';
import { UO } from '../../../models/unidad_organica';
import { UOService } from '../../../services/uo.service';
import { EO } from '../../../models/escala_ocupacional';
import { EOService } from '../../../services/eo.service';
import { CN } from '../../../models/canton';
import { CNService } from '../../../services/can.service';
import { DEService } from '../../../services/de.service';
import Swal from 'sweetalert2';
import { error } from 'protractor';


@Component({
  selector: 'regper',
  templateUrl: './personal.component.html',
  providers: [PerService, PAService, NOService, RLService, DPService, MLService, UOService, EOService, CNService,DEService],

})

export class RegisterperComponent implements OnInit {
  public title: String;
  public per: Per;
  public status: string;
  public ci_correcta: boolean;
  public partida_individual_unica: boolean;
  public ci_unica: boolean;
  public cedulas: any[];
  public num: boolean;
  public num1: boolean;
  public pa: PAD;
  public pas: PAD[] = [];
  public bufferpas: PAD[] = [];

  public no: NO;
  public nos: NO[] = [];
  public bufferno: NO[] = [];

  public rl: RL;
  public rls: RL[] = [];
  public bufferrl: RL[] = [];

  public dp: DP;
  public dps: DP[] = [];
  public bufferdp: DP[] = [];

  public ml: ML;
  public mls: ML[] = [];
  public bufferml: ML[] = [];

  public uo: UO;
  public uos: UO[] = [];
  public bufferuo: UO[] = [];

  public eo: EO;
  public eos: EO[] = [];
  public buffereo: EO[] = [];

  public cn: CN;
  public cns: CN[] = [];
  public buffercn: CN[] = [];
  public identity;
  constructor(
    private _route: ActivatedRoute,
    private _router: Router,
    private _perService: PerService,
    private _paService: PAService,
    private _noService: NOService,
    private _rlService: RLService,
    private _dpService: DPService,
    private _mlService: MLService,
    private _uoService: UOService,
    private _eoService: EOService,
    private _cnService: CNService,
    private _deService: DEService
  ) {
    this.initpas();
    this.initno();
    this.initrl();
    this.initdp();
    this.initml();
    this.inituo();
    this.initeo();
    this.initcn();
    this.initCedulas();
    this.identity = JSON.parse(localStorage.getItem('identity'));
    if (!this.identity) {
        this._router.navigate(['/home']);
      }
    this.title = 'Registro de Personal';
    this.per = new Per('',
      '',
      '',
      '1401',
      '',
      '',
      '',
      '32014010000900000000300051160000100000000',
      '',
      '',
      '',
      '',
      '',
      '',
      '',
      'PASTAZA',
      '',
      '',
      '',
      '',
      '',
      '',
      '',
      '',
      true);
  }
  cedulaUnica(cedula) {
    this.ci_unica = true;
    for (const cedulas of this.cedulas) {
        const cedulaBD = cedulas.cedula;
        // console.log(cedulaBD);
        if (cedulaBD == cedula) {

          this.ci_unica = false;
        }
    }
  }
  partida_individualUnica(partida_individual) {
    this.partida_individual_unica = true;
    for (const partida_individuals of this.cedulas) {
        const partida_individualBD = partida_individuals.partida_individual;
        // console.log(partida_individualBD);
        if (partida_individualBD == partida_individual) {
          this.partida_individual_unica = false;
        }
    }
  }
  comprobar_ci(cedula) {
    this.ci_correcta = false;
    if (cedula.length === 10) {
      let cedulaCache: any[];
      let sumaPares = 0;
      let sumaImpares = 0;
      let sumaTotal: number;
      let verificador: number;
      let comparador = 10;
      cedulaCache = cedula.split('');
      for (let i = 0; i < 9; i = i + 2) {
        let impar = cedulaCache[i];
        impar = impar * 2;
        if (impar >= 10) {
          impar = impar - 9;
        }
        sumaImpares = sumaImpares + impar;
      }
      for (let i = 1; i < 8; i = i + 2) {
        const par = cedulaCache[i];
        sumaPares = sumaPares + parseInt(par, 0);
      }
      sumaTotal = sumaPares + sumaImpares;
      while (comparador < sumaTotal) {
        comparador = comparador + 10;
      }
      if (comparador - sumaTotal <= 9) {
        verificador = comparador - sumaTotal;
        if (verificador == cedulaCache[9]) {
          this.ci_correcta = true;
        } else {
          this.ci_correcta = false;
        }
      }
    }
  }
  getData(event) {
    this.pas = JSON.parse(event);

  }
  ngOnInit() {
    console.log('Componente de registro de funcionarios cargado.....');
  }
  changeToUpperCase() {
    this.per.apellidos = this.per.apellidos.toUpperCase();
    this.per.nombres = this.per.nombres.toUpperCase();
  }
  onSubmit(form) {
    this.changeToUpperCase();
    this._perService.register(this.per).subscribe(
      response => {
        console.log(response);
        if (response.PER && response.PER._id) {

          this.status = 'success';
          form.reset();
          this.initCedulas();
        } else {
          this.status = 'error';
        }
      },
      error => {
        console.log(<any>error);
      }
    );
  }
  initCedulas() {
    this._deService.getCedulasper().subscribe(
      response => {
        this.cedulas = response.cedulas;
        console.log(this.cedulas);
      }
    );
  }
  initpas() {
    this._paService.getpa().subscribe(
      response => {
        this.pas = response.PADI;
        this.bufferpas = response.PADI;
        console.log(this.pas);
      }, error => {
        console.error(error as any);
      }
    )
  }
  initno() {
    this._noService.getno().subscribe(
      response => {
        this.nos = response.NOC;
        this.bufferno = response.NOC;
        console.log(this.nos);
      }, error => {
        console.error(error as any);
      }
    )
  }
  initrl() {
    this._rlService.getrl().subscribe(
      response => {
        this.rls = response.RLA;
        this.bufferrl = response.RLA;
        console.log(this.rls);
      }, error => {
        console.error(error as any);
      }
    )
  }

  initdp() {
    this._dpService.getdp().subscribe(
      response => {
        this.dps = response.DDPU;
        this.bufferdp = response.DDPU;
        console.log(this.dps);
      }, error => {
        console.error(error as any);
      }
    )
  }

  initml() {
    this._mlService.getml().subscribe(
      response => {
        this.mls = response.MLA;
        this.bufferml = response.MLA;
        console.log(this.mls);
      }, error => {
        console.error(error as any);
      }
    )
  }

  inituo() {
    this._uoService.getuo().subscribe(
      response => {
        this.uos = response.UOR;
        this.bufferuo = response.UOR;
        console.log(this.uos);
      }, error => {
        console.error(error as any);
      }
    )
  }

  initeo() {
    this._eoService.geteo().subscribe(
      response => {
        this.eos = response.EOS;
        this.buffereo = response.EOS;
        console.log(this.eos);
      }, error => {
        console.error(error as any);
      }
    )
  }


  initcn() {
    this._cnService.getcn().subscribe(
      response => {
        this.cns = response.CAN;
        this.buffercn = response.CAN;
        console.log(this.cns);
      }, error => {
        console.error(error as any);
      }
    );
  }

  comprobarCadena(cadena:string) {
    const numeros = '0123456789';
    this.num = false;
    let hasNumbers: boolean = false;
    if (cadena && cadena.length) {
      for (const caracter of cadena) {
        if (numeros.indexOf(caracter) != -1) {
          hasNumbers = true;
          this.num = true;
        }
      }
    }
    if (hasNumbers == true) {
      Swal.fire({
        icon:'error',
        title: 'Datos Incorrectos',
        text: 'No se permiten numeros en este campo',
        timer: 2500
      })
    }
  }
  comprobarCadenan(cadena:string) {
    const numeros = '0123456789';
    this.num1 = false;
    let hasNumbers: boolean = false;
    if (cadena && cadena.length) {
      for (const caracter of cadena) {
        if (numeros.indexOf(caracter) != -1) {
          hasNumbers = true;
          this.num1 = true;
        }
      }
    }
    if (hasNumbers == true) {
      Swal.fire({
        icon:'error',
        title: 'Datos Incorrectos',
        text: 'No se permiten numeros en este campo',
        timer: 2500
      })
    }
  }

}

