import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { DP } from '../../../models/denominacion_puesto';
import { DPService } from '../../../services/dp.service';
import { FuncService } from '../../../services/fun.service';

@Component({
    selector: 'listdp',
    templateUrl: './denominacion_puesto.component.html',
    providers: [DPService, FuncService]
})

export class ListardpComponent {
    public title: String;
    public dp: DP;
    public Searchdp: string;
    public dps: DP[] = [];
    public bufferdp: DP[] = [];
    public identity;

    constructor(
        private _dpService: DPService,
        private _funcService: FuncService,
        private _router:Router
    ) {
        this.title = 'Listar Denominaciones de Puestos';
        this.initdp();
        this.identity = this._funcService.getIdentity();

        this.identity = JSON.parse(localStorage.getItem('identity'));
        if (!this.identity) {
            this._router.navigate(['/home']);
        }
    }

    initdp() {
        this._dpService.getdp().subscribe(
            response => {
                this.dps = response.DDPU;
                this.bufferdp = response.DDPU;
                console.log(this.dps);
            }, error => {
                console.error(error as any);
            }
        )
    }

    getData(event) {
        this.dps = JSON.parse(event);

    }
    find(termino) {
        termino = termino.toLowerCase().replace(/ /g, '');
        this.dps = [];
        if (this.bufferdp && this.bufferdp.length >= 1) {
            for (const anc of this.bufferdp) {
                let datos = anc.denominacion_puesto + anc.codigo;
                if (anc.denominacion_puesto) {
                    datos = datos + anc.denominacion_puesto;
                }
                datos = datos.toLowerCase().replace(/ /g, '');
                datos = datos.toLowerCase().replace(/-/g, '');
                if ((datos.indexOf(termino)) >= 0) {
                    this.dps.push(anc);

                }
            }
        }
    }


}
