import { Component, OnInit } from '@angular/core';
import {Router, ActivatedRoute, Params} from '@angular/router';
import { Func } from '../../../models/funcionario';
import { FuncService } from '../../../services/fun.service';
import Swal from 'sweetalert2';

@Component({
    selector: 'fun-edit-admin',
    templateUrl: './fun-edit-admin.component.html',
    providers: [FuncService]
  })
  export class funadmineditComponent {
    public title:string;
    public fun: Func;
    public status: string;
    public identity;
    public token;
    public num: boolean;
    public num1: boolean;
    constructor(
        private _route: ActivatedRoute,
        private _router: Router,
        private _funService: FuncService
    ){
    this.title = 'Actualizar datos';
    //this.fun = this._funService.getIdentity();
    //this.token =  this._funService.getToken();
    this.identity = JSON.parse(localStorage.getItem('identity'));
        if (!this.identity || this.identity.role === 'ROLE_USER') {
            this._router.navigate(['/home']);
        }

      this.initFunc();
    }
    initFunc() {
      this._route.params.subscribe(
        params => {
          const id = params.id;
          this._funService.getfun(id).subscribe(
            response => {
              this.fun = response.FUN;
            }
          )
        }
      )
    }
  ngOnInit(){
      console.log('fun-edit-admin cargado');
  }

  onSubmit(){
      console.log(this.fun);
      this._funService.updateFuns(this.fun).subscribe(
          response => {
              if(!response.fun){
                this.status = 'error';
              }else{
                this.status = 'success';
              }
          },
          error => {
              var errorMessage = <any>error;
              console.log(errorMessage);
              
              if(errorMessage != null){
                  this.status = 'error';
              }
          }
      );
  }
  comprobarCadena(cadena:string) {
    this.num = false
    const numeros = '0123456789';
    let hasNumbers: boolean = false;
    if (cadena && cadena.length) {
      for (const caracter of cadena) {
        if (numeros.indexOf(caracter) != -1) {
          hasNumbers = true;
          this.num = true;
        }
      }
    }
    if (hasNumbers == true) {
      Swal.fire({
        icon:'error',
        title: 'Datos Incorrectos',
        text: 'No se permiten numeros en este campo',
        timer: 2500
      })
    }
  }
  comprobarCadenan(cadena:string) {
    const numeros = '0123456789';
    this.num1 = false;
    let hasNumbers: boolean = false;
    if (cadena && cadena.length) {
      for (const caracter of cadena) {
        if (numeros.indexOf(caracter) != -1) {
          hasNumbers = true;
          this.num1 = true;
        }
      }
    }
    if (hasNumbers == true) {
      Swal.fire({
        icon:'error',
        title: 'Datos Incorrectos',
        text: 'No se permiten numeros en este campo',
        timer: 2500
      })
    }
  }
  }