import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { CN } from '../../../models/canton';
import { CNService } from '../../../services/can.service';
import { FuncService } from '../../../services/fun.service';

@Component({
    selector: 'listcn',
    templateUrl: './canton.component.html',
    providers: [CNService, FuncService]
})

export class ListarcnComponent {
    public title: String;
    public cn: CN;
    public Searchcn: string;
    public cns: CN[] = [];
    public buffercn: CN[] = [];
    public identity;

    constructor(
        private _cnService: CNService,
        private _funcService: FuncService
    ) {
        this.title = 'Listar Cantones';
        this.initcn();
        this.identity = this._funcService.getIdentity();
    }

    initcn() {
        this._cnService.getcn().subscribe(
            response => {
                this.cns = response.CAN;
                this.buffercn = response.CAN;
                console.log(this.cns);   
            }, error => {
                console.error(error as any);
            }
        )
    }
    
    getData(event) {
        this.cns = JSON.parse(event);

    }
    find(termino) {
        termino = termino.toLowerCase().replace(/ /g, '');
        this.cns = [];
        if (this.buffercn && this.buffercn.length >=1 ) {
            for (const anc of this.buffercn) {
                let datos = anc.nombre + anc.codigo;
                if (anc.nombre) {
                    datos = datos + anc.nombre;
                }
                datos = datos.toLowerCase().replace(/ /g, '');
                datos = datos.toLowerCase().replace(/-/g, '');
                if ((datos.indexOf(termino)) >= 0) {
                    this.cns.push(anc);
                    
                }
            }
        }
    }


}
