// tslint:disable: variable-name
import { Component, ViewChild, ElementRef } from '@angular/core';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { AP } from '../../../models/accion_personal';
import { APService } from '../../../services/ap.service';
import * as faker from 'faker'; // Not really require by jspdf or jsPDF-AutoTable 
declare var jsPDF: any;
import htmlToImage from 'html-to-image';
@Component({
  selector: 'app-impap',
  templateUrl: './accion_personal.component.html',
  providers: [APService],
  
  styleUrls: ['./accion_personal.component.css']
})

export class ImprimirapComponent {
  @ViewChild('content', {static: false}) content: ElementRef;
  public title: string;
  public ap: AP;
  public identity;
  constructor(
    private _route: ActivatedRoute,
    private _router: Router,
    private _apService: APService,
  ) {
    this.title = 'Imprimir de Acciones de Personal';
    this.getAP();
    this.identity = JSON.parse(localStorage.getItem('identity'));
    if (!this.identity) {
      this._router.navigate(['/home']);
    }
   }

   getAP() {
     this._route.params.subscribe(
       params => {
        if (params.id) {
          this._apService.getAp(params.id).subscribe(
            response => {
              this.ap = response.AP;
              console.log(this.ap);
            }
          );
        }
       }
     );
   }
   public generarPdf() {
    // let doc = new jsPDF();
    // let specialElementHandlers = {
    //   '#editor' : function (element, renderer) {
    //     return true;
    //   }
    // };
    // let content = this.content.nativeElement;
    const title = this.ap.codigo_ap + '_' + this.ap.ci_per;
    let page1;
    let node1 = document.getElementById('contenido');
    let page2;
    let node2 = document.getElementById('contenido1');
    htmlToImage.toPng(node1)
      .then(function (dataUrl) {
        page1 = dataUrl
        htmlToImage.toPng(node2)
          .then(function (dataUrl) {
            page2 = dataUrl
    
            const doc = new jsPDF();
            doc.setFontSize(40);
            doc.addImage(page1, 'PNG', 3, 5, 200, 285);
            doc.addPage();
            doc.addImage(page2, 'PNG', 3, 5, 200, 142);
            doc.save(title);
        });

    });
    // doc.fromHTML(content.innerHTML, 15, 15, {
    //   'width': 190,
    //   'elementHandlers': specialElementHandlers
    // });
    // 
    // doc.save('test.pdf')

  }
  public printPdf() {
    // let doc = new jsPDF();
    // let specialElementHandlers = {
    //   '#editor' : function (element, renderer) {
    //     return true;
    //   }
    // };
    // let content = this.content.nativeElement;
    const title = this.ap.codigo_ap + '_' + this.ap.ci_per;
    let page1;
    let node1 = document.getElementById('contenido');
    let page2;
    let node2 = document.getElementById('contenido1');
    htmlToImage.toPng(node1)
      .then(function (dataUrl) {
        page1 = dataUrl
        htmlToImage.toPng(node2)
          .then(function (dataUrl) {
            page2 = dataUrl
    
            const doc = new jsPDF();
            doc.setFontSize(40);
            doc.addImage(page1, 'PNG', 3, 5, 200, 285);
            doc.addPage();
            doc.addImage(page2, 'PNG', 3, 5, 200, 142);
            //doc.save(title);
            doc.output("dataurlnewwindow");
            doc.autoPrint();
        });

    });
    // doc.fromHTML(content.innerHTML, 15, 15, {
    //   'width': 190,
    //   'elementHandlers': specialElementHandlers
    // });
    // 
    // doc.save('test.pdf')

  }

}
