import { Component, OnInit } from '@angular/core';
import {Router, ActivatedRoute, Params} from '@angular/router';
import { Func } from '../../models/funcionario';
import { FuncService } from '../../services/fun.service';

@Component({
  selector: 'login',
  templateUrl: './login.component.html',
  providers: [FuncService]
})
export class LoginComponent implements OnInit {
    public title:string;
    public fun: Func;
    public status: string;
    public identity;
    public token;

    constructor(
        private _route: ActivatedRoute,
        private _router: Router,
        private _funService: FuncService
    ){
        this.title = 'Identificacion';
        this.identity = JSON.parse(localStorage.getItem('identity'));
        if (this.identity) {
            this._router.navigate(['/home']);
        }
        this.fun = new Func('',
            '',
            '',
            '',
            '',
            '',
            '',
            '',
            '',
            '',
            '',
            true);
    }
    ngOnInit(){
        console.log('Componente de login cargado...');
    }

    onSubmit(){
        //alert(this.fun.correo);
        //alert(this.fun.password);
        //console.log(this.fun);
        this._funService.signup(this.fun).subscribe(
            response => {
                this.identity = response.fun;
                if(this.identity.activa === true){
                if(!this.identity || !this.identity._id){
                    this.status = 'error';
                }else{
                    this.status = 'success';
                    //persistir datos de usuario
                    localStorage.setItem('identity', JSON.stringify(this.identity));
                    //conseguir token
                    this.getToken();                    
                    
                }
            }else{
                let alerta = alert('El Usuario que intenta loguear esta bloquedo');
            }
                
            },
            error => {
                var errorMessage = <any>error;
                console.log(errorMessage);

                if(errorMessage !=null){
                    this.status = 'error';
                }                
            }
        );
    }

    getToken(){
        this._funService.signup(this.fun, 'true').subscribe(
            response => {
                this.token = response.token;

                if(this.token.length <= 0){
                    this.status = 'error';
                    alert('No hay token' + response);
                }else{
                    this.status = 'success';
                    //persistir token de usuario
                    localStorage.setItem('token', JSON.stringify(this.token));
                    window.location.reload();
                }
                
                
            },
            error => {
                var errorMessage = <any>error;
                console.log(errorMessage);

                if(errorMessage !=null){
                    this.status = 'error';
                }                
            }
        );
    }

}
