import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { NO } from '../../../models/nivel_ocupacional';
import { NOService } from '../../../services/no.service';
import { FuncService } from '../../../services/fun.service';

@Component({
    selector: 'listno',
    templateUrl: './nivel_ocupacional.component.html',
    providers: [NOService, FuncService]
})

export class ListarnoComponent {
    public title: String;
    public no: NO;
    public Searchno: string;
    public nos: NO[] = [];
    public bufferno: NO[] = [];
    public identity;

    constructor(
        private _noService: NOService,
        private _funcService: FuncService,
        private _router: Router
    ) {
        this.title = 'Listar Niveles Ocupacionales';
        this.initno();
        this.identity = this._funcService.getIdentity();
        this.identity = JSON.parse(localStorage.getItem('identity'));
        if (!this.identity) {
            this._router.navigate(['/home']);
          }
    }

    initno() {
        this._noService.getno().subscribe(
            response => {
                this.nos = response.NOC;
                this.bufferno = response.NOC;
                console.log(this.nos);   
            }, error => {
                console.error(error as any);
            }
        )
    }
    
    getData(event) {
        this.nos = JSON.parse(event);

    }
    find(termino) {
        termino = termino.toLowerCase().replace(/ /g, '');
        this.nos = [];
        if (this.bufferno && this.bufferno.length >=1 ) {
            for (const anc of this.bufferno) {
                let datos = anc.nivel_ocupacional + anc.codigo;
                if (anc.nivel_ocupacional) {
                    datos = datos + anc.nivel_ocupacional;
                }
                datos = datos.toLowerCase().replace(/ /g, '');
                datos = datos.toLowerCase().replace(/-/g, '');
                if ((datos.indexOf(termino)) >= 0) {
                    this.nos.push(anc);
                    
                }
            }
        }
    }


}