import {Injectable} from '@angular/core';
import { HttpClient, HttpHeaders} from '@angular/common/http';
import { Observable } from 'rxjs';
import {global} from './global';
import {UO} from '../models/unidad_organica';

@Injectable()

export class UOService{
    public url: string;
    public identity;
    public token;
    public rol;

    constructor(public _http: HttpClient){
        this.url = global.url;
    }

    register(uo: UO): Observable<any>{
        let params = JSON.stringify(uo);
        let headers = new HttpHeaders().set('Content-Type', 'application/json')
                                    .set('Authorization', this.getToken());

        return this._http.post(this.url+ 'registraruo', params, {headers:headers})
    }

    getIdentity(){
        let identity = JSON.parse(localStorage.getItem('identity'));

        if(identity != 'undefined'){
            this.identity = identity;
        }else{
            this.identity = null;
        }

        return this.identity;
    }
    
    getToken(){
        let token = localStorage.getItem('token');

        if(token != 'undefined'){
            this.token = token;
        }else{
            this.token = null;
        }

        return this.token;
    }

    // updateFun(fun : Func): Observable<any>{
    //     let params = JSON.stringify(fun);
    //     let headers = new HttpHeaders().set('Content-Type', 'application/json')
    //                                    .set('Authorization', this.getToken());

    //     return this._http.put(this.url+'actualizarfun/'+fun._id, params, {headers: headers});

    // }
    getuo(): Observable<any> {
        const headers = new HttpHeaders().set('Content-Type', 'application/json')
          .set('Authorization', this.getToken());
        return this._http.get(this.url + 'listaruo', { headers });
      }
      getuos(id): Observable<any> {
        const headers = new HttpHeaders().set('Content-Type', 'application/json')
          .set('Authorization', this.getToken());
        return this._http.get(this.url + 'listaruos/' + id, { headers });
      }

      updateuo(uo: UO): Observable<any>{
        const params = JSON.stringify(uo);
        const headers = new HttpHeaders().set('Content-Type', 'application/json')
        .set('Authorization', this.getToken());
        return this._http.put(this.url + 'editaruo/'+uo._id, params , {headers});
      }

}