import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { AN } from '../../../models/autoridad_nomidora';
import { ANService } from '../../../services/an.service';
import { DEService } from '../../../services/de.service';

@Component({
    selector: 'regan',
    templateUrl: './autoridad_nominadora.component.html',
    providers: [ANService,DEService]
})

export class RegisteranComponent {
    public title: String;
    public an: AN;
    public status: string;
    public status1: string;
    public editar: boolean;
    public an_unica: boolean;
    public ans: any[];
    public identity;
    constructor(
        private _route: ActivatedRoute,
        private _router: Router,
        private _anService: ANService,
        private _deService: DEService
    ) {
        this.title = 'Registro de Autoridad Nominadora';
        this.editar = false;
        this.initAN();
        this.initANEdit();
        this.initand();
        this.identity = JSON.parse(localStorage.getItem('identity'));
        if (!this.identity) {
            this._router.navigate(['/home']);
          }
    }
    initand() {
        this._deService.getanm().subscribe(
          response => {
            this.ans = response.ans;
            console.log(this.ans);
          }
        );
      }
      anUnica(nombre) {
        this.an_unica = true;
        for (const ans of this.ans) {
            const anBD = ans.nombre;
            // console.log(cedulaBD);
            if (anBD === nombre) {
              this.an_unica = false;
            }
        }
      }
    initAN() {
        this.an = {
            _id: '',
            nombre: '',
            activa: true,
            autor: '',
            fecha_autor: '',
            editor: '',
            fecha_editor : ''
        };
    }

    initANEdit() {
        this._route.params.subscribe(
            params => {
                console.log(params.id);
                if (params.id) {
                    this._anService.getan(params.id).subscribe(
                        response => {
                            if (response.AN) {
                                this.an = response.AN;
                                this.editar = true;
                            }
                        }
                    )
                }
            }
        )
    }
    onSubmit(form) {
        if (this.editar == true) {
            this.onEdit(form);
        } else if (this.editar == false) {
            this.onNew(form);
        }
    }
    onEdit(form){
        
        this._anService.updateAn(this.an).subscribe(
            response => {
                if (response.AN && response.AN._id) {
                    // console.log(response.fun);
                    this.status1 = 'success';
                    
                } else {
                    this.status1 = 'error';
                }
            },
            error => {
                console.log(<any>error);
            }
            
        );
    }
    onNew(form) {
        this._anService.register(this.an).subscribe(
            response => {
                if (response.AN && response.AN._id) {
                    // console.log(response.fun);
                    this.status = 'success';
                    form.reset();
                } else {
                    this.status = 'error';
                }
            },
            error => {
                console.log(<any>error);
            }
        );
    }
    
}



