import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { EO } from '../../../models/escala_ocupacional';
import { EOService } from '../../../services/eo.service';
import { FuncService } from '../../../services/fun.service';

@Component({
    selector: 'listeo',
    templateUrl: './escala_ocupacional.component.html',
    providers: [EOService, FuncService]
})

export class ListareoComponent {
    public title: String;
    public eo: EO;
    public Searcheo: string;
    public eos: EO[] = [];
    public buffereo: EO[] = [];
    public identity;

    constructor(
        private _eoService: EOService,
        private _funcService: FuncService,
        private _router: Router
    ) {
        this.title = 'Listar Escala Ocupacional';
        this.initeo();
        this.identity = this._funcService.getIdentity();
        this.identity = JSON.parse(localStorage.getItem('identity'));
        if (!this.identity) {
            this._router.navigate(['/home']);
          }
    }

    initeo() {
        this._eoService.geteo().subscribe(
            response => {
                this.eos = response.EOS;
                this.buffereo = response.EOS;
                console.log(this.eos);   
            }, error => {
                console.error(error as any);
            }
        )
    }
    
    getData(event) {
        this.eo = JSON.parse(event);

    }
    find(termino) {
        termino = termino.toLowerCase().replace(/ /g, '');
        this.eos = [];
        if (this.buffereo && this.buffereo.length >=1 ) {
            for (const anc of this.buffereo) {
                let datos = anc.escala_ocupacional + anc.codigo;
                if (anc.escala_ocupacional) {
                    datos = datos + anc.escala_ocupacional;
                }
                datos = datos.toLowerCase().replace(/ /g, '');
                datos = datos.toLowerCase().replace(/-/g, '');
                if ((datos.indexOf(termino)) >= 0) {
                    this.eos.push(anc);
                    
                }
            }
        }
    }


}