import { Component, OnInit } from '@angular/core';
import { COService } from '../../../services/co.service';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { CO } from '../../../models/codigo';
import { APService } from '../../../services/ap.service';
import Swal from 'sweetalert2';

@Component({
    selector: 'app-codigo',
    templateUrl: './codigo.component.html',
    providers: [COService, APService]
})
export class CodigoComponent implements OnInit {
    public status: string;
    public status1: string;
    public title: string;
    public editar: boolean;
    public co;
    public codigos;
    public identity;
    public alln_unica: boolean;
    public meme: boolean;
    public today;
    public jo;
    constructor(
        private _route: ActivatedRoute,
        private _coService: COService,
        private _router: Router,
        private _apService: APService
    ) {
        this.title = 'codigo';
        this.status = null;
        this.editar = false;
        this.status1 = null;
        this.alln_unica = null;
        this.identity = JSON.parse(localStorage.getItem('identity'));
        if (!this.identity || this.identity.role === 'ROLE_USER') {
            this._router.navigate(['/home']);
        }
        this.co = {
            codigo: ''
            // created_at: (new Date()).toString,
            // created_by: this.identity._id
        };
        this.comprobarIngreso();
        this.initCodigos();
        this.meme = false;
        this.today = new Date();
        this.jo = this.today.getFullYear();
    }
    initCodigos() {
        this._apService.getCodigos().subscribe(
            response => {
                console.log(response);
                this.codigos = [];
                if (response.Codigos) {
                    for (const codigos of response.Codigos) {
                        codigos.codigo_ap = codigos.codigo_ap.replace('-', '');
                        const codigo = codigos.codigo_ap.slice(3, 7);
                        this.codigos.push(codigo);
                    }
                }
                console.log(this.codigos);
            }
        );
    }
    compararCodigo(codigoCompleto) {
        this.initCodigos();
        const codigo = this.co.codigo.toUpperCase();
        console.log('comparando', codigo, ' (', codigo.length, ')');
        if (codigo.length >= 4) {
            let contador = 0;
            for (const codigoComparar of this.codigos) {
                if (codigoComparar == codigo) contador++;
            }
            if (contador >= 1) {
                Swal.fire({
                    icon: 'error',
                    title: 'Esta nomenclatura ya se uso',
                    text: 'No se puede volver a utilizar una nomenclatura',
                })
                this.meme = true;
            }else{
                this.meme = false;
            }
        }
    }
    onSubmit(registermlForm) {
        console.log(this.co.codigo);
        const rango = '001';
        const codigo = this.co.codigo;
        const anio = this.jo;
        this.co.codigo = rango + '-' + codigo.toUpperCase() + '-' + anio;
        console.log(this.co.codigo);
        if (this.editar == true) {
            this.onEdit(registermlForm);
        } else if (this.editar == false) {
            this.onNew(registermlForm);
        }

    }
    ngOnInit() {

    }
    initCO() {
        this.co = new CO('',
            ''
        );
    }
    initCOEdit() {
        this._route.params.subscribe(
            params => {
                console.log(params.id);
                if (params.id) {
                    this._coService.getco().subscribe(
                        response => {
                            if (response.ML) {
                                this.co = response.COD;
                                this.editar = true;
                            }
                        }
                    );
                }
            }
        );
    }

    onEdit(form) {
        this._coService.updateco(this.co).subscribe(
            response => {
                console.log(response);

                if (response.CO && response.CO._id) {
                    this.status1 = 'success';

                } else {
                    this.status1 = 'error';
                }
            },
            error => {
                console.log(error as any);
            }

        );
    }
    onNew(form) {
        this._coService.register(this.co).subscribe(
            response => {
                if (response.CO && response.CO._id) {
                    console.log(response.CO);
                    this.status = 'success';
                } else {
                    this.status = 'error';
                }
            },
            error => {
                console.log(error as any);
            }
        );
    }
    comprobarIngreso() {
        this._coService.getco().subscribe(
            response => {
                console.log(response);
                this.co = response.COD[0];
                this.editar = true;
            }, error => {
                console.log(error as any);

            }
        );
    }
}
