import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { Per } from '../../../models/personal';
import { PerService } from '../../../services/pers.service';
import { Subscriber } from 'rxjs';
import Swal from 'sweetalert2';


@Component({
    selector: 'listper',
    templateUrl: './personal.component.html',
    providers: [PerService],
    styleUrls: ['./personal.component.css']
})

export class ListarperComponent {
    public title: String;
    public per: Per;
    public Searchper: string;
    public pers: Per[] = [];
    public bufferpers: Per[] = [];
    public identity;
    public fecha = new Date();
    public ano;
    public verper: Per;

    constructor(
        private _percService: PerService,
        private _router: Router
    ) {
        this.title = 'Listar Personal';
        this.initper();
        this.identity = this._percService.getIdentity();
        this.ano= this.fecha.getFullYear();
        this.identity = JSON.parse(localStorage.getItem('identity'));
        if (!this.identity) {
            this._router.navigate(['/home']);
          }
    }
    imprimir() {
        window.print();
    }
    initper() {
        this.pers = [];
        this.bufferpers = [];
        this._percService.getpers().subscribe(
            response => {
                for (const per of response.PERS) {
                    if (per.activa === true) {
                        this.pers.push(per);
                        this.bufferpers.push(per);
                    }
                }
                console.log(this.pers);
            }, error => {
                console.error(error as any);
            }
        )
    }
    cambioEstado(personal) {
        Swal.fire({
            title: 'Desea desactivar a este empleado?',
            text: "Una vez desactivado pasara a la lista de personal inactivo",
            icon: 'warning',
            showCancelButton: true,
            confirmButtonColor: '#3085d6',
            cancelButtonColor: '#d33',
            confirmButtonText: 'Si, Desactivar!'
        }).then((result) => {
            if (result.value) {
                Swal.fire(
                    'Desactivado!',
                    'El empleado ha sido desactivado',
                    'success'
                )
                if (personal.activa === true) {
                    personal.activa = false;
                } else {
                    personal.activa = true;
                }
                this._percService.cambiarEstado(personal).subscribe(
                    response => {
                        console.log(response);
                        this.initper();

                    }, error => {
                        console.log(error as any);

                    }
                    );
                }
            })


        }

    getData(event) {
        this.pers = JSON.parse(event);

    }
    find(termino) {
        termino = termino.toLowerCase().replace(/ /g, '');
        // termino = termino.split(';');
        // console.log(termino);
        this.pers = [];
        if (this.bufferpers && this.bufferpers.length >=1 ) {
            for (const per of this.bufferpers) {
                let datos = per.nombres + per.apellidos + per.canton + per.cedula;
                if (per.nombres) {
                    datos = datos + per.nombres;
                }
                datos = datos.toLowerCase().replace(/ /g, '');
                datos = datos.toLowerCase().replace(/-/g, '');
                if ((datos.indexOf(termino)) >= 0) {
                    this.pers.push(per);
                }
            }
        }
    }


}
