import {Component, OnInit} from '@angular/core';
import {Router, ActivatedRoute, Params} from '@angular/router';
import {UO} from '../../../models/unidad_organica';
import {UOService} from '../../../services/uo.service';
import { DEService } from '../../../services/de.service';

@Component({
    selector: 'reguo',
    templateUrl: './unidad_organica.component.html',
    providers: [UOService, DEService]
})

export class RegisteruoComponent{
    public title: String;
    public uo: UO;
    public status: string;
    public status1: string;
    public editar: boolean;
    public alln_unica: boolean;
    public allc_unica: boolean;
    public alls: any[];
    public identity;
    constructor( 
        private _route: ActivatedRoute,
        private _router: Router,
        private _uoService : UOService,
        private _deService: DEService
    ){
        this.title = 'Registro de Unidad Orgánica';
        this.editar = false;
        this.initUO();
        this.initUOEdit();
        this.initall();
        this.identity = JSON.parse(localStorage.getItem('identity'));
        if (!this.identity) {
            this._router.navigate(['/home']);
          }
    }
    initall() {
        this._deService.getuor().subscribe(
            response => {
                this.alls = response.uo;
                console.log(this.alls);
            }
        );
    }
    allnUnica(unidad_organica) {
        this.alln_unica = true;
        for (const alls of this.alls) {
            const allBD = alls.unidad_organica;
             //console.log(allBD);
            if (allBD === unidad_organica) {

                this.alln_unica = false;
            }
        }
    }
    allcUnica(codigo) {
        this.allc_unica = true;
        for (const alls of this.alls) {
            const allBD = alls.codigo;
            // console.log(cedulaBD);
            if (allBD === codigo) {

                this.allc_unica = false;
            }
        }
    }
    initUO(){
        this.uo = new UO('',
        '',
        '',
        '',
        '',
        '',
        '');
    }
    initUOEdit() {
        this._route.params.subscribe(
            params => {
                console.log(params.id);
                if (params.id) {
                    this._uoService.getuos(params.id).subscribe(
                        response => {
                            if (response.UO) {
                                this.uo = response.UO;
                                this.editar = true;
                            }
                        }
                    )
                }
            }
        )
    }
    onSubmit(form) {
        if (this.editar == true) {
            this.onEdit(form);
        } else if (this.editar == false) {
            this.onNew(form);
        }
    }
    onEdit(form){
        this._uoService.updateuo(this.uo).subscribe(
            response => {
                if (response.UO && response.UO._id) {
                    this.status1 = 'success';
                    
                } else {
                    this.status1 = 'error';
                }
            },
            error => {
                console.log(<any>error);
            }
            
        );
    }
    onNew(form) {
        this._uoService.register(this.uo).subscribe(
            response => {
                if (response.UO && response.UO._id) {
                    // console.log(response.fun);
                    this.status = 'success';
                    form.reset();
                    this.initall();
                } else {
                    this.status = 'error';
                }
            },
            error => {
                console.log(<any>error);
            }
        );
    }
    }