import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { CN } from '../../../models/canton';
import { CNService } from '../../../services/can.service';
import { DEService } from '../../../services/de.service';

@Component({
    selector: 'regcn',
    templateUrl: './canton.component.html',
    providers: [CNService, DEService]
})

export class RegistercnComponent {
    public title: String;
    public cn: CN;
    public status: string;
    public status1: string;
    public editar: boolean;
    public identity;
    public cn_unica: boolean;
    public cnc_unica: boolean;
    public cns: any[];
    constructor(
        private _route: ActivatedRoute,
        private _router: Router,
        private _cnService: CNService,
        private _deService: DEService
    ) {
        this.title = 'Registro de Canton';
        this.editar = false;
        this.initCN();
        this.initCNEdit();
        this.initcnd();
        this.identity = JSON.parse(localStorage.getItem('identity'));
        if (!this.identity) {
            this._router.navigate(['/home']);
          }
    }
    initcnd() {
        this._deService.getcan().subscribe(
            response => {
                this.cns = response.cns;
                console.log(this.cns);
            }
        );
    }
    cnUnica(nombre) {
        this.cn_unica = true;
        for (const cns of this.cns) {
            const cnBD = cns.nombre;
            // console.log(cedulaBD);
            if (cnBD === nombre) {

                this.cn_unica = false;
            }
        }
    }
    cncUnica(codigo) {
        this.cnc_unica = true;
        for (const cns of this.cns) {
            const cnBD = cns.codigo;
            // console.log(cedulaBD);
            if (cnBD === codigo) {

                this.cnc_unica = false;
            }
        }
    }
    initCN() {
        this.cn = new CN('',
            '',
            '',
            '',
            '',
            '',
            '');
    }
    initCNEdit() {
        this._route.params.subscribe(
            params => {
                console.log(params.id);
                if (params.id) {
                    this._cnService.getcns(params.id).subscribe(
                        response => {
                            if (response.CA) {
                                this.cn = response.CA;
                                this.editar = true;
                            }
                        }
                    )
                }
            }
        )
    }
    onSubmit(form) {
        if (this.editar == true) {
            this.onEdit(form);
        } else if (this.editar == false) {
            this.onNew(form);
        }
    }
    onEdit(form) {
        this._cnService.updatecn(this.cn).subscribe(
            response => {
                if (response.CA && response.CA._id) {
                    this.status1 = 'success';

                } else {
                    this.status1 = 'error';
                }
            },
            error => {
                console.log(<any>error);
            }

        );
    }
    onNew(form) {
        this._cnService.register(this.cn).subscribe(
            response => {
                if (response.CA && response.CA._id) {
                    // console.log(response.fun);
                    this.status = 'success';
                    form.reset();
                } else {
                    this.status = 'error';
                }
            },
            error => {
                console.log(<any>error);
            }
        );
    }
}
