import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs';
import { global } from './global';
import { DP } from '../models/denominacion_puesto';

@Injectable()

export class DPService {
    public url: string;
    public identity;
    public token;
    public rol;

    constructor(public _http: HttpClient) {
        this.url = global.url;
    }

    register(dp: DP): Observable<any> {
        let params = JSON.stringify(dp);
        let headers = new HttpHeaders().set('Content-Type', 'application/json')
            .set('Authorization', this.getToken());

        return this._http.post(this.url + 'registrardp', params, { headers: headers })
    }

    getIdentity() {
        let identity = JSON.parse(localStorage.getItem('identity'));

        if (identity != 'undefined') {
            this.identity = identity;
        } else {
            this.identity = null;
        }

        return this.identity;
    }

    getToken() {
        let token = localStorage.getItem('token');

        if (token != 'undefined') {
            this.token = token;
        } else {
            this.token = null;
        }

        return this.token;
    }

    // updateFun(fun : Func): Observable<any>{
    //     let params = JSON.stringify(fun);
    //     let headers = new HttpHeaders().set('Content-Type', 'application/json')
    //                                    .set('Authorization', this.getToken());

    //     return this._http.put(this.url+'actualizarfun/'+fun._id, params, {headers: headers});

    // }
    getdp(): Observable<any> {
        const headers = new HttpHeaders().set('Content-Type', 'application/json')
            .set('Authorization', this.getToken());
        return this._http.get(this.url + 'listardp', { headers });
    }
    getdps(id): Observable<any> {
        const headers = new HttpHeaders().set('Content-Type', 'application/json')
            .set('Authorization', this.getToken());
        return this._http.get(this.url + 'listardps/' + id, { headers });
    }

    updatedp(dp: DP): Observable<any> {
        const params = JSON.stringify(dp);
        const headers = new HttpHeaders().set('Content-Type', 'application/json')
            .set('Authorization', this.getToken());
        return this._http.put(this.url + 'editardp/' + dp._id, params, { headers });
    }

}